import React, { Component } from 'react';
import { getCashRegister } from '../../actions/appAction';
import globalState from '../../global/GlobalState';
import {loading} from '../../global/GlobalFunctions';
import ContentMesas from '../mesas/ContentMesas';
import Cajas from './Cajas';


class Content extends Component {

	constructor(props){
		super(props);
    this.state = {
      title : '',
      cajas : ''
    };

    this.changeSate(this.props);
	}

  changeSate = (Props) => {
    var ajaxCajas  = getCashRegister(Props.params.restaurantId)
    , _this        = this
    , jsonResponse = ''
    , cajas        = []
    , params       = {}
    , title        = ''
    // console.log(Props);
    // ABRIR LOADING
    loading('on','Cargando Cajas');
    ajaxCajas.then(function(data) {
      jsonResponse = JSON.parse(data)
      // console.log(jsonResponse,Props);return;
      // console.log(params);
        if (jsonResponse.status==='success'){
          //SI SOLO HAY UNA CAJA EN ESE AMBIENTE Y ESTA ABIERTA ENTONCES NO MOSTRAR LA CAJA SI NO LAS MESAS
          // console.log(jsonResponse,Props);return;
          if (jsonResponse.estado_caja_unica==='Abierta'){
            // console.log(jsonResponse);
            globalState.dispatch({
              type   : "cashRegisterData",
              params : {
                          id     : jsonResponse.id_caja_unica,
                          nombre : jsonResponse.nombre_caja_unica,
                          id_seccion : jsonResponse.id_seccion

              }
            });

            params = { restaurantId: _this.props.params.restaurantId }
            var send = {
                contentView : <ContentMesas params={params} />,
                params      : {}
            };
            globalState.dispatch({
                  type   : "contetActual",
                  params : send

            });
            // console.log(jsonResponse);
            let botons = globalState.getState().navBtns;
            botons.forEach((element, key) => {
              if(botons[key].id==='monitor' || botons[key].id==='user'){
               botons[key].visible=true;
              }
              else{
               botons[key].visible=false;
              }
            });

            globalState.dispatch({
              type   : "navBtns",
              params : botons
            });

            return;
          }

          Object.keys(jsonResponse.cajas).forEach((key) => {
            params = { idCaja : jsonResponse.cajas[key].id ,nombre : jsonResponse.cajas[key].nombre, restaurantId : Props.params.restaurantId }
            cajas.push( <Cajas key={jsonResponse.cajas[key].id} icon="" params={params} />);
          });
        }
        else{
          // params = { idCaja : 0 ,nombre : 'no hay cajas configuradas' }
          // cajas.push( <Cajas key='1' icon="" name='Sin Cajas' params={params} />);
          // cajas = '';
          title = '(No hay Cajas configuradas)';
        }

        if (Props.params.restaurantIdBodega<=0){
          _this.setState({
              title : `El restaurante ${Props.params.restaurantName} no tiene configurada la bodega  ${title}`,
              cajas : ''
          });
        }
        else{
          _this.setState({
              title : `Seleccione la caja de ${_this.props.params.restaurantName} ${title}`,
              cajas : cajas
          });
        }
        // CERRAR LOADING
        loading();
        // _this.changeSate(cajas);
    });


  }



  componentDidUpdate(prevProps, prevState, snapshot){
    // if (this.props.userID !== prevProps.userID) {
    //   this.fetchData(this.props.userID);
    // }
    // console.log(this.props);
    // console.log(prevProps, prevState, snapshot);
  }

  actualizaBotones = ()=>{
    let botons = globalState.getState().navBtns;
    botons.forEach((element, key) => {
      if(botons[key].id!=='user'){
       botons[key].visible=false;
      }
    });

    globalState.dispatch({
      type   : "navBtns",
      params : botons
    });
  }


  shouldComponentUpdate(nextProps){
      if (this.props.params.restaurantId != nextProps.params.restaurantId){
        this.changeSate(nextProps);

      }
      document.getElementById('AsideRIcon').style.display='none';
      this.actualizaBotones();




      return true;
  }

  render() {
    // console.log(this.state.cajas);
    return (
            [
              <div key="1" className="title">
                {this.state.title}
              </div>,
              <div key="2" className="">
                {this.state.cajas}
              </div>

            ]
    );
  }
}

export default Content;