import React, { Component } from 'react';
import globalState from '../../global/GlobalState';
import {loading} from '../../global/GlobalFunctions';
import {deleteItem} from '../../actions/appAction'

class ItemCuenta extends Component {

  constructor(props){
    super(props);

  }

  deleteItem = () => {
    if (window.confirm("Desea eliminar el item?") ) {
      let ajaxDelete = deleteItem(this.props.params.id_cuenta,this.props.params.id,this.props.params.id_item)
      ,   jsonResponse = ''
      ,   items = globalState.getState().headerRights.items
      delete items[this.props.arrayKey]
      // console.log(typeof(globalState.getState().headerRights.items));
      loading('on','Eliminando Item');
      ajaxDelete.then(function(jsonResponse) {
        if (jsonResponse.status==='success'){
          globalState.dispatch({
            type   : "headerRights",
            params : {items:items}
          });
        }
        else{
          alert(`Error\n${jsonResponse.message}`);
        }
        loading();
      });
    }

  }

  printComanda = () =>{
   // console.log(this.props.params.id_comanda);
    if (window.confirm("El item ya fue comandado, no se puede modificar, desea imprimir la comanda?")) {
      this.props.funcPrint(this.props.params.id_comanda);
    }

  }

  anularComanda = ()=>{
     let styleForm = {
       width : "150px",
       display : "block",
     }

     let params = {
            id      : ``,
            title   : `Autorizar eliminacion`,
            width   : `300px`,
            height  : `300px`,
            html    : <div>
                        <div className="form_separador" materialdesign="true">
                            <div >Ingrese los datos del autorizador</div>
                          </div>
                          <div >
                            <div  materialdesign="true" className="form_content_field input-filled" style={styleForm} >
                              <input type="text" className="with-label" obligatorio="true" id="username" data-value="" data-required="true" data-style="none"   />
                              <label className="floating-label" >Usuario</label>
                            </div>
                            <div  materialdesign="true" className="form_content_field input-filled" style={styleForm} >
                              <input type="password" className="with-label" obligatorio="true" id="password" data-value="" data-required="true" data-style="none"   />
                              <label className="floating-label" >Contrasea</label>
                            </div>
                          </div>
                      </div> ,
            tbar    : [
                        {
                          id      : '',
                          cls     : 'check_circle',
                          text    : 'Anular',
                          handler : ()=>{
                            let _this = this;
                            let params = {
                              username: document.getElementById("username").value,
                              password: document.getElementById("password").value
                            };
                            if(params.username === '' || params.password == ''){
                              alert("Debe llenar los campos solicitados")
                              return
                            }

                            let ajaxDelete = deleteItem(_this.props.params.id_cuenta,_this.props.params.id,_this.props.params.id_item,params.username,params.password)
                            ,   jsonResponse = ''
                            // console.log(typeof(globalState.getState().headerRights.items));
                            loading('on','Eliminando Item');
                            ajaxDelete.then(function(jsonResponse) {
                              if (jsonResponse.status==='success'){
                                let items = globalState.getState().headerRights.items
                                delete items[_this.props.arrayKey]

                                globalState.dispatch({
                                  type   : "headerRights",
                                  params : {items:items}
                                });
                              }
                              else{
                                alert(`Error\n${jsonResponse.message}`);
                              }
                              loading();
                            });                      
                            window.location.href = "#";
                          }
                        }
                      ],
            
      }
       window.location.href = "#win-modal";
       globalState.dispatch({
        type   : "winData",
        params : params
    });
    }

  render() {

    return (

        <div className="itemDetail">
            <div title="item" onClick={this.props.params.comandado===true ? this.printComanda.bind() : ()=>{} }>
              <div className="itemCantidad">{this.props.params.cantidad} </div>
              <div className="nameItem">
                {this.props.params.nombre_item}
                <br/>
                <span>$ {new Intl.NumberFormat().format(this.props.params.precio)}</span>
              </div>
            </div>
          <div className="deleteItem" style={{display:"flex", alignItems: "center"}}>
            {this.props.params.comandado===true ? (
              <>
              <i className="material-icons" title="Comandado" style={{marginRight: "8px"}} onClick={this.printComanda.bind()} >room_service</i>
              <i className="material-icons" title="Eliminar comanda" onClick={this.anularComanda.bind()} >delete</i>
              </>
              ):
              <i className="material-icons" style={{marginLeft:"18px"}}onClick={this.deleteItem.bind()} >delete</i>
            }
          </div>
        </div>
    );
  }
}

export default ItemCuenta;