import React, { Component } from 'react';
import imageLogo from '../logo.png';

class Logo extends Component {
  render() {
    return (
      <div className="Logo">
      	<div className="contenedorLogo">
	      	<div className="imageLogo">
	      		<img src={imageLogo}  width="30"/>
	      	</div>
	      	<div className="TextLogo">
	      		Logicalsoft-POS
	      	</div>
	     </div>
      </div>
    );
  }
}

export default Logo;