import React, { Component } from 'react';
import logo_default from '../../images/default.png'
import globalState from '../../global/GlobalState';
import Ingredientes from './Ingredientes';

const cantidad = [
  {label:1,value:1},{label:2,value:2},{label:3,value:4},{label:5,value:5},{label:6,value:6},{label:7,value:7},{label:8,value:8},{label:9,value:9},{label:10,value:10}
];

const ApiConfig = window.env;

class ItemwinData extends Component {

	constructor(props){
		super(props);
    this.state = {
      terminos     : [],
      ingredientes : [],
      keyPrecios   : 6,
      precios      : []
    }
    // console.log(globalState.getState().restaurantData.cambiaPrecio);
    // console.log(this.state);
    // console.log(this.props);
	}

  changeSate = (Props) => {
    // console.log(Props.params)
    let terminos   = []
    , ingredientes = []
    , precios      = []

    this.setState({
      terminos     : [],
      ingredientes : [],
      precios      : []
    });

    precios.push(<option key={1} value={Props.params.precio_venta_1} >{new Intl.NumberFormat().format( Props.params.precio_venta_1)}</option> );
    precios.push(<option key={2} value={Props.params.precio_venta_2} >{new Intl.NumberFormat().format( Props.params.precio_venta_2)}</option> );
    precios.push(<option key={3} value={Props.params.precio_venta_3} >{new Intl.NumberFormat().format( Props.params.precio_venta_3)}</option> );
    precios.push(<option key={4} value={Props.params.precio_venta_4} >{new Intl.NumberFormat().format( Props.params.precio_venta_4)}</option> );
    precios.push(<option key={5} value={Props.params.precio_venta_5} >{new Intl.NumberFormat().format( Props.params.precio_venta_5)}</option> );

    this.setState({
      precios  : precios,
    });

    if (typeof(Props.params.termino)==='object'){
      Props.params.termino.terminos.forEach((element) => {
        // terminos.push({ label: element.termino, value: element.id_termino },);
        terminos.push(<option key={element.id_termino} value={element.termino} >{element.termino}</option> );
        // console.log(element)
      });
      this.setState({
        terminos : terminos,
      });
    }
    if (typeof(Props.params.receta)==='object'){
      Props.params.receta.forEach((element) => {
        ingredientes.push(<Ingredientes key={element.id} params={element} />);
      });
      this.setState({
        ingredientes : ingredientes
      });
    }

  }

  componentDidMount(){
    this.changeSate(this.props)
  }

  shouldComponentUpdate(nextProps){
    // console.log(this.props , nextProps)
      if (this.props.params.id != nextProps.params.id){
        // console.log(this.props , nextProps)
        this.changeSate(nextProps);
      }

      return true;
  }

  addPrice = ()=> {
    var precio = prompt("Por favor introduzca el nuevo precio", "");
    if (precio != null) {
      let precios      = []
      this.setState({
        precios      : []
      });

      precios.push(<option key={1} value={this.props.params.precio_venta_1} >{new Intl.NumberFormat().format( this.props.params.precio_venta_1)}</option> );
      precios.push(<option key={2} value={this.props.params.precio_venta_2} >{new Intl.NumberFormat().format( this.props.params.precio_venta_2)}</option> );
      precios.push(<option key={3} value={this.props.params.precio_venta_3} >{new Intl.NumberFormat().format( this.props.params.precio_venta_3)}</option> );
      precios.push(<option key={4} value={this.props.params.precio_venta_4} >{new Intl.NumberFormat().format( this.props.params.precio_venta_4)}</option> );
      precios.push(<option key={5} value={this.props.params.precio_venta_5} >{new Intl.NumberFormat().format( this.props.params.precio_venta_5)}</option> );
      precios.push(<option key={6} selected value={precio} >{new Intl.NumberFormat().format( precio)}</option> );

      this.setState({
        precios  : precios,
      });
    }
    // else{
    //   console.log(precio);
    //   alert("Debe ingresar un valor");
    // }
  }

  render() {
    // console.log(this.props.params.receta);
    let styleForm = {
      width : "100%",
      height : "100%"
    }
    let logo = `${ApiConfig.urlServer}ARCHIVOS_PROPIOS/empresa_${globalState.getState().userData.id_host}/panel_de_control/documentos_items/${this.props.params.logo}`
    return (
      <div style={styleForm}>
        <div className="principalIngredientes">
          <img className="imageItem" />
          <div className="inputsSelect">
            <div className="camposSelect">
              <input type="tex" placeholder="Cantidad" data-validate="integer" id="cantidad_item" />
            </div>
            <div className="camposSelect">
              <select id="termino_item" >
                <option value="" disabled={true} >Termino</option>
                {this.state.terminos}
              </select>
            </div>
            <div className="camposSelect selectPrice">
              <select id="precio_item" >
                <option value="" disabled={true} >Precio</option>
                {this.state.precios}
              </select>
              {
                globalState.getState().restaurantData.cambiaPrecio !== 'No' ?
                <i className="material-icons" onClick={this.addPrice} title="Agregar Precio" >playlist_add</i> : ("")
              }
            </div>
              <textarea placeholder='Observaciones' id="observaciones_item" />
          </div>
        </div>
        <div className="contentIngredientes">
          <div className="ingredientes TituloIngrediente" >
            <div className="ingredientesName ">
              <div>
                &nbsp; Ingredientes
              </div>
            </div>
            <div className="">
            </div>
          </div>
          <div id="contentIngredients">
            {this.state.ingredientes}
          </div>
        </div>
      </div>
    );
  }
}

export default ItemwinData;