import React, { Component } from 'react';
import globalState from '../global/GlobalState';
import ContentCajas from './Cajas/ContentCajas'
// import $ from "jquery";

import SliderbarItems from './SliderbarItems'

class Sliderbar extends Component {

	constructor(props){
    // console.log(globalState.getState().userData.nitEmpresa);
		super(props);
    var _this = this;
    var jsonResponse = ''
    ,   restaurants  = []
    this.props.modules.then(function(data) {
        jsonResponse = JSON.parse(data)
        if (jsonResponse.status==='success'){
          Object.keys(jsonResponse.restaurants).forEach((key) => {
            let params = {
                restaurantId       : jsonResponse.restaurants[key].id,
                restaurantIdBodega : jsonResponse.restaurants[key].id_bodega,
                restaurantName     : jsonResponse.restaurants[key].nombre,
                cambiaPrecio       : jsonResponse.restaurants[key].cambia_precio_items
            }
            let content = (globalState.getState().userData.acceso_caja===true)? "ContentCajas" : "ContentMesas" ;

            restaurants.push( <SliderbarItems
                                key={jsonResponse.restaurants[key].id}
                                icon=""
                                funchandleClick={_this.changeStateContent}
                                moduleItem={content}
                                params={params}/>);
          });
        }
        else{
          restaurants.push( <SliderbarItems icon="" funchandleClick="" name='Sin restaurantes' moduleItem='' />);
        }
        _this.changeSate(restaurants);
    });

    this.state = {
      restaurants : ''
    };

	}

  changeStateContent = (params) => {

     var send = {
        contentView : <ContentCajas params={params} />,
    };
    globalState.dispatch({
          type   : "contetActual",
          params : send

    });
    globalState.dispatch({
      type   : "headerRights",
      params : {content:''}
    });
  }

  handleClick= () => {
    this.props.funchandleClick('ContentCajas');
  }


  changeSate = (restaurants) => {
    this.setState({
        restaurants : restaurants
    });
  }

  render() {
    return (
      <div className="sidebar" id="sidebar">

      	<ul>
          {this.state.restaurants}
      	</ul>
      </div>
    );
  }
}

export default Sliderbar;