import React, { Component } from 'react';
import globalState from '../../global/GlobalState';

class MetodosPagoFila extends Component {

	constructor(props){
		super(props);
    this.state={
      n_aprobacion : '',
      n_tarjeta    : ''
    };
    this.method = React.createRef();
	}

  metodosPago = e =>{
    var elementos = document.getElementsByClassName("inputMetodos");
    var respuesta = [];
    for (var objectEl in elementos) {
      if(typeof elementos[objectEl].id!=='undefined'){
        let nameInput = document.getElementById(`name${elementos[objectEl].id}`).innerHTML;
        let valorElemento='';
        // if(elementos[objectEl].getAttribute('categoria')===e.target.getAttribute('categoria')){
           valorElemento= elementos[objectEl].value;
        // }else{
          // elementos[objectEl].value='';
        // }
        //
        //
        respuesta.push(
                        {
                          id           : elementos[objectEl].id,
                          valor        : valorElemento,
                          nombre       : nameInput,
                          categoria    : elementos[objectEl].getAttribute('categoria'),
                          tipo         : elementos[objectEl].getAttribute('tipo'),
                          n_aprobacion : elementos[objectEl].getAttribute('n_aprobacion'),
                          n_tarjeta    : elementos[objectEl].getAttribute('n_tarjeta')
                        }
                      );
      }
    }
    this.props.funMetodos(respuesta);
  }

  changeMetodoPagoInfo = () =>{
    // console.log(this.method.current);
    let styleForm = {
      width : "340px",
      display : "block",
    }
    var _this = this
    let params = {
              id     : ``,
              title  : `Datos de la transaccion`,
              width  : `400px`,
              height : `300px`,
              html   : <div>
                          <div className="form_separador" materialdesign="true">
                            <div >Registrar detalle transaccion</div>
                          </div>
                          <div  materialdesign="true" className="form_content_field input-filled" style={styleForm} >
                            <input type="text" className="with-label" obligatorio="false" id="n_aprobacion_field" data-value="" data-required="true" data-style="none"   />
                            <label className="floating-label" >No Aprobacion</label>
                          </div>
                          <div  materialdesign="true" className="form_content_field input-filled" style={styleForm}  >
                            <input type="text" className="with-label" obligatorio="false"  id="n_tarjeta_field"  data-value="" data-required="true" data-style="none" />
                            <label className="floating-label" >Digitos tarjeta</label>
                          </div>
                        </div>,
              tbar   : [
                        {
                          id      : '',
                          cls     : 'check_circle',
                          text    : 'Guardar',
                          handler : ()=>{
                            let n_aprobacion = document.getElementById('n_aprobacion_field')
                            ,   n_tarjeta    = document.getElementById('n_tarjeta_field')

                            this.method.current.setAttribute("n_aprobacion" ,n_aprobacion.value);
                            this.method.current.setAttribute("n_tarjeta" ,n_tarjeta.value);

                            // this.setState({
                            //   n_aprobacion : documento.value,
                            //   n_tarjeta    : nombre.value,
                            // });
                            // this.props.type               = 'comensal';
                            // this.props.documento_comensal = documento;
                            // this.props.name               = nombre;
                            n_aprobacion.value='';
                            n_tarjeta.value='';
                            window.location.href = "#";

                            var elementos = document.getElementsByClassName("inputMetodos");
                            var respuesta = [];
                            for (var objectEl in elementos) {
                              if(typeof elementos[objectEl].id!=='undefined'){
                                let nameInput = document.getElementById(`name${elementos[objectEl].id}`).innerHTML;
                                let valorElemento='';
                                valorElemento= elementos[objectEl].value;
                                // if(elementos[objectEl].getAttribute('categoria')===e.target.getAttribute('categoria')){
                                // }else{
                                //   elementos[objectEl].value='';
                                // }
                                //
                                //
                                respuesta.push(
                                                {
                                                  id           : elementos[objectEl].id,
                                                  valor        : valorElemento,
                                                  nombre       : nameInput,
                                                  categoria    : elementos[objectEl].getAttribute('categoria'),
                                                  tipo         : elementos[objectEl].getAttribute('tipo'),
                                                  n_aprobacion : elementos[objectEl].getAttribute('n_aprobacion'),
                                                  n_tarjeta    : elementos[objectEl].getAttribute('n_tarjeta')
                                                }
                                              );
                              }
                            }
                            _this.props.funMetodos(respuesta);

                          },
                        },
                      ],
        }

    window.location.href = "#win-modal";
    globalState.dispatch({
        type   : "winData",
        params : params
    });

  }


  render() {
    let IdElemetn='name'+this.props.id;
    return (
      <div className="contenedorMetodosPago">
          <div id={IdElemetn} className=" noBorder">{this.props.name} </div>
          <div className=" center noBorder">
            <input id={this.props.id} type="text" ref={this.method} categoria={this.props.categoria} tipo={this.props.tipo} className="inputMetodos" onKeyUp={this.metodosPago}/>
          </div>
          {
            this.props.tipo === 'Tarjeta Credito' || this.props.tipo === 'Tarjeta Debito' ?
            (<div className=" center"> 
              <i className="material-icons" onClick={this.changeMetodoPagoInfo} title="Ingresar los datos de transaccion" >description</i>
              </div>):
              ("")
          }
      </div>
    );
  }
}

export default MetodosPagoFila;