import React, { Component } from 'react';
import MetodosPagoFila from './MetodosPagoFila';
import globalState from '../../global/GlobalState';

class MetodosPagoTipo extends Component {

	constructor(props){
		super(props);
    var metodos_pago = globalState.getState().metodos_pago[this.props.categoria];
     var metodosA = [];
    for (var i = 0;i < metodos_pago.length;i++) {
      metodosA.push(<MetodosPagoFila key={i} categoria={this.props.categoria} id={metodos_pago[i].id} tipo={metodos_pago[i].tipo}  name={metodos_pago[i].nombre} funMetodos={this.props.funMetodos}/>);
    }


    this.state={
      metodos:metodosA
    };
	}

  render() {
    return (
        <div className="acordeon__item">
          <input type="radio" name="acordeon"  className="acordeonInput" id={this.props.categoria} />
          <label htmlFor={this.props.categoria} className="acordeon__titulo">{this.props.categoria} </label>
          <div className="acordeon__contenido">
              {this.state.metodos}
          </div>
        </div>
    );
  }
}

export default MetodosPagoTipo;