import React, { Component } from 'react';
import './ErrorPage.css'

class ErrorPage extends Component {

	constructor(props){
		super(props)
	}

	render() {
	    return (
	    		[
				<div key="1" className="wrap">
					<div className="logo">
						<h1>{this.props.statusCode}</h1>
						<p>{this.props.responseText}</p>
					</div>
				</div>,
				<div key="2" className="footer">
					LogicalSoft-POS 2019. All Rights Reserved | Design by <a href="https://logicalsoft.co">LogicalSoft S.A.S</a>
				</div>
				]
	    );
	}
}

export default ErrorPage;