import { createStore } from 'redux';

/**
 * globalState Estado global con parametros usados en diferentes componentes
 * type:
 * contetActual     = Estado del contenedor principal en donde se renderizan las cajas, mesas, items, etc
 * userData         = Datos del usuario que inicio sesion en el POS
 * modalData        = Datos que se renderizaran en la ventana modal al abrirla
 * restaurantData   = Datos del restaurante seleccionado
 * cashRegisterData = Datos de la caja seleccionada
 * mesaData         = Datos de la mesa seleccionada
 * headerRights     = Datos del aside derec
 * loadingData      = Configuracion para mostrar el loading
 * navBtns          = lista con los botones del menu superior de la barra de navegacion
 *
 */
const globalState = (state={ params: {} },action) =>{
  return {
              ...state,
              type:action.type,
             [action.type] : action.params
        }
}

export default createStore(globalState,{params: {}});