import React, { Component } from 'react';
import File from './../modules/files/Files'
import ContentMesas from './mesas/ContentMesas'
import ContentCajas from './Cajas/ContentCajas'
import globalState from './../global/GlobalState';

var updateComponent='';
class Content extends Component {


	constructor(props){
		super(props);
    this.state = {
        contentView:'',
        contentParams:''
    };


    globalState.subscribe( ()=>{
      if(typeof globalState.getState().contetActual!=="undefined"){

        if(globalState.getState().contetActual.contentView!=='' & globalState.getState().contetActual.contentView!==this.state.contentView){
          this.changeState(globalState.getState().contetActual);
          updateComponent=true;
        }
      }

    });

	}

 shouldComponentUpdate(nextProps, nextState) {

    if(nextProps.contentView!==this.state.contentView  || nextProps.params!==this.state.contentParams || updateComponent===true){
        globalState.dispatch({
          type   : "contetActual",
          params : nextProps

        });
      updateComponent=false;
      return true;
    }else{
        console.log(" No actualizo");

      return false;
    }
  }

  changeState = (params)=>{
      this.setState({
        contentView   : params.contentView,
        contentParams : params.params
      });
  }



  render() {


    return (
      <div className="Content">
      	  { this.state.contentView === 'ContentCajas' ? (
                    < ContentCajas params={this.state.contentParams} />

            ):this.state.contentView === 'ContentMesas' ? (
                    < ContentMesas params={this.state.contentParams} />

            )  : this.state.contentView }
      </div>
    );
  }
}

export default Content;