/**
 * formValidate Validaciones a aplicar a los formularios
 */
export function formValidate(){
	// console.log("sdfsdfsdfsd");
	let div        = document.querySelector(".win-modal")
	,	campos     = div.querySelectorAll("input, textarea, select")
	,	type       = ''
	,	key        = ''
	,	validate   = ''
	,	arrayValue = ''
	,	value      = ''
	,	result     = ''
	,	max        = ''
	,	min        = ''

	campos.forEach(function(input) {
	  // console.log(element);
	  type = input.getAttribute('data-validate')
		switch (type){
			case 'integer' :
				input.className += " win-input-number";
				input.onkeypress = (event) =>
					_validateInteger({ event:event, eventType:'keypress', input:input })

					input.addEventListener("change", (event) =>{
					_validateInteger({ event:event, eventType:'change', input:input })
						}
					)
			break;

			case 'double' :
				input.className += " win-input-number"
				 input.onkeypress = (event)=>
				 	_validateDouble({ event:event, eventType:'keypress', input:input })
					input.addEventListener( "change", (event) =>{
						// console.log(input);
						// _validateDouble({ event:event, eventType:'change', input:input })
						}
					)
			break;

			case 'real' :
				input.className += " win-input-number";

				input.onkeypress = (event)=> {
					return _validateReal({ event:event, eventType:'keypress', input:input })
				}

				input.onchange = (event)=> {
					return _validateReal({ event:event, eventType:'change', input:input })
				}
			break;

			case 'uppercase' :
				input.className += " win-input-uppercase"

				input.addEventListener( "change", (event) =>{
					_validateUpperCase({ event:event, eventType:'change', input:input })
					}
				)
			break;


			case 'lowercase' :
				input.className += " win-input-lowercase"

				input.addEventListener( "change", (event) =>{
					_validateLowerCase({ event:event, eventType:'change', input:input })
					}
				)
			break;

			case 'email' :
				input.addEventListener( "change", (event) =>{
					_validateEmail({ event:event, input:input })
					}
				)
			break;

			case 'percent' :
				input.addEventListener( "change", (event) => {
					_validatePercent({ event:event, input:input })
					}
				)
			break;

			case 'date' :
				// ##_validateDate(input)
				// ##$W.DatePicker idApply:input.id
				// new ($W.DatePicker)(idApply:input.id)
			break;

			case 'time' :
				// ##_validateDate(obj)
				// ##new ($W.DatePicker)(_validateDate(obj))
				// $('#'+input.id).clockpicker
				// 	placement: 'bottom'
				// 	align: 'left'
				// 	autoclose: true
				// 	'default': 'now'
			break;

			default:
				input.onkeyup = (event)=>{
					_validateText({ event:event, eventType:'keyup', input:input })
				}

				input.onchange = (event)=> {
					_validateText({ event:event, eventType:'change', input:input })
				}
		}
	});
	/*
	[].forEach.call(campos, (input)=>

		)
	*/
	var _validateInteger = (obj) => {
		key = (document.all)? obj.event.keyCode : obj.event.which ;
		if (_keyEnable(key)){ return true }
		else if (obj.eventType === 'keypress'){ return (/\d/).test(String.fromCharCode(key)) }
		else if (obj.eventType === 'change'){ obj.input.value = (obj.input.value).replace(/[^\d.]/g,'') }
	}

	var _validateDouble = (obj) => {
		key = ( document.all)? obj.event.keyCode : obj.event.which;

		if (_keyEnable(key) ){ return true }
		else if (obj.eventType === 'keypress'){ return (/[\d.]/).test(String.fromCharCode(key)) }
		else if (obj.eventType === 'change'){
			obj.input.value = (obj.input.value).replace(/[^\d.]/g,'')
			validate = !(obj.input.value).toString().match(/(^-?\d\d*[\.|,]\d*$)|(^-?\d\d*$)|(^-?[\.|,]\d\d*$)/)
			if (!validate ){
				arrayValue = (obj.input.value).split(".")
				obj.input.value = arrayValue[0]+'.'+arrayValue[1]
			}
		}
	}

	var _validateReal = (obj) => {
		key = (document.all)? obj.event.keyCode : obj.event.which;

		if (_keyEnable(key)) { return true }
		else if (obj.eventType === 'keypress'){ return (/[\d.-]/).test(String.fromCharCode(key)) }
		else if (obj.eventType === 'change' ){
			value = obj.input.value
			if (value === ''){ return }
			value = value.replace(/[^\d.-]/g,'')
			if (isNaN(value) ){ obj.input.value = '' }
			else{ obj.input.value = value }
		}
	}

	var _validateText = (obj) =>{
		key = (document.all)? obj.event.keyCode : obj.event.which;

		if (_keyEnable(key)){ return true }
		// # else if obj.eventType == 'keypress' then return (/[^\#\-\"\']/).test(String.fromCharCode(key))
		else if (obj.eventType == 'change'){ obj.input.value = (obj.input.value).replace(/[\#\"\']/g,'') }
	}

	var _validateUpperCase = (obj) =>{
		key = (document.all)? obj.event.keyCode : obj.event.which;
		if( _keyEnable(key)) {return true}
		else {obj.input.value = ((obj.input.value).replace(/[\#\-\"\']/g,'')).toUpperCase()}
		// true
	}

	var _validateLowerCase = (obj) =>{
		key = (document.all)? obj.event.keyCode : obj.event.which;
		if( _keyEnable(key)) {return true}
		else {obj.input.value = ((obj.input.value).replace(/[\#\-\"\']/g,'')).toUpperCase()}
		// true
	}

	var _validateEmail = (obj) =>{
		// validate = !!(obj.input.value).toString().match(/(^[a-z0-9]([0-9a-z\-_\.]*)@([0-9a-z_\-\.]*)([.][a-z]{3})$)|(^[a-z]([0-9a-z_\.\-]*)@([0-9a-z_\-\.]*)(\.[a-z]{2,4})$)/i)
		validate = !(obj.input.value).toString().match(/(^[a-z0-9]([0-9a-z\-_\.]*)@([0-9a-z_\-\.]*)([.][a-z]{3})$)|(^[a-z]([0-9a-z_\.\-]*)@([0-9a-z_\-\.]*)(\.[a-z]{2,4})$)/i)
		if (!validate){
			obj.input.value=""
			obj.input.focus()
		}
	}

	var _validatePercent = (obj) => {
		max = obj.input.getAttribute('data-max') * 1
		min = obj.input.getAttribute('data-min') * 1
		result = obj.input.value * 1
		if (result <= max){
			if (result >= min){
				console.log ('valor aceptado' )
			}
			else{
				alert ('no se aceptan  menores a ' + min)
				obj.input.value = ''
				obj.input.focus()
			}
		}
		else{
			alert ('no se aceptan muneros mayores a' + max)
			obj.input.value = ''
			obj.input.focus()
		}

		return true
	}

	var _keyEnable = (key) =>{
		// ###
		// # KEY CODE
		// #
		// # key==8 		// BACKSPACE
		// # key==9 		// TAB
		// # key==0 		// TAB
		// # key==13 		// ENTER
		// # key==37 		// CURSOR LEFT
		// # key==39 		// CURSOR RIGHT
		// # key==38 		// CURSOR TOP
		// # key==40 		// CURSOR BOTTOM
		// #
		// ###
		if (key==8 || key==9 || key==0 || key==13 || key==37 || key==38 || key==39 || key==40) { return true }
		else {return false}
	}

	var _validateDate = (obj) =>{

		obj.yearRange                       = (obj.yearRange )? obj.yearRange : 10;
		obj.bound                           = (obj.bound )? obj.bound : undefined;
		obj.position                        = (obj.position )? obj.position : 'bottom left';
		obj.reposition                      = (obj.reposition )? obj.reposition : true;
		obj.format                          = (obj.format )? obj.format : 'YYYY-MM-DD';
		obj.defaultDate                     = (obj.defaultDate )? obj.defaultDate : null;
		obj.setDefaultDate                  = (obj.setDefaultDate )? obj.setDefaultDate : false;
		obj.firstDay                        = (obj.firstDay )? obj.firstDay : 1;
		obj.formatStrict                    = (obj.formatStrict )? obj.formatStrict : false;
		obj.minDate                         = (obj.minDate )? obj.minDate : null;
		obj.maxDate                         = (obj.maxDate )? obj.maxDate : null;
		obj.showWeekNumber                  = (obj.showWeekNumber )? obj.showWeekNumber : false;
		obj.minYear                         = (obj.minYear )? obj.minYear : 0;
		obj.maxYear                         = (obj.maxYear )? obj.maxYear : 9999;
		obj.minMonth                        = (obj.minMonth )? obj.minMonth : undefined;
		obj.maxMonth                        = (obj.maxMonth )? obj.maxMonth : undefined;
		obj.startRange                      = (obj.startRange )? obj.startRange : null;
		obj.endRange                        = (obj.endRange )? obj.endRange : null;
		obj.isRTL                           = (obj.isRTL )? obj.isRTL : false;
		obj.yearSuffix                      = (obj.yearSuffix )? obj.yearSuffix : '';
		obj.showMonthAfterYear              = (obj.showMonthAfterYear )? obj.showMonthAfterYear : false;
		obj.showDaysInNextAndPreviousMonths = (obj.showDaysInNextAndPreviousMonths )? obj.showDaysInNextAndPreviousMonths : false;
		obj.numberOfMonths                  = (obj.numberOfMonths )? obj.numberOfMonths : 1;
		obj.mainCalendar                    = (obj.mainCalendar )? obj.mainCalendar : 'left';
		obj.container                       = (obj.container )? obj.container : undefined;
		// ##obj.i18n = if

		// values =
		// 	idApply: obj.idApply
		// 	yearRange : obj.yearRange
	}
}

		// $W.VerifiIsInputFilled(document.getElementById(id))

