import React, { Component } from 'react';
import Button from './Button';

class Tbar extends Component {

	constructor(props){
		super(props);
	}

	render(){
		var tbar    = null
		,	buttons = []
		// console.log(this.props.params.tbar);
		if (this.props.params.tbar !== undefined) {
			// console.log(this.props.tbar, Object.keys(this.props.tbar).length);
			if (Object.keys(this.props.params.tbar).length > 0 ){
				Object.keys(this.props.params.tbar).forEach((key) => {
					buttons.push((<Button key={key} params={this.props.params.tbar[key]} />));
				});
		      	tbar = <div style={{overflowY:"hidden",overflowX:"hidden"}} >
			      			<div id='tbar{this.props.id}' /*style=""*/ className="win-tbar" data-role="win-tbar">
			      				{buttons}
							</div>
						</div>;
			}
		}

		return( tbar );
	}
}

export default Tbar;

