import React, { Component } from 'react';
import globalState from '../../global/GlobalState';
import {loading} from '../../global/GlobalFunctions';
import { getItems } from '../../actions/appAction';
import './Items.css'
import Items from './Items'
class ContentItems extends Component {

	constructor(props){
		super(props);
    this.state = {
      items : []
    }
    this.getItems();
    this.actualizaBotones();
    // console.log(globalState.getState().mesaData);
	}

  getItems = (cadena='') =>{
    var ajaxItems  = getItems(globalState.getState().userData.id_empleado,globalState.getState().userData.nitEmpresa,globalState.getState().userData.username,globalState.getState().userData.token,cadena)
    , jsonResponse = ''
    , items        = []
    , params       = {}
    , _this        = this

    loading('on','Cargando Items');
    ajaxItems.then(function(jsonResponse){

      if(jsonResponse)
        {
        if(jsonResponse.length>0){
          jsonResponse.forEach((element) => {
            items.push( <Items key={element.id} params={element} />);
          });
        }
      }

      _this.setState({
        items : items
      });

      loading();

    });
  }

  SearchItemsInput = (event)=>{
    if(event.key !== 'Enter'){return;}
    var SearchItemsInputs = document.getElementById("SearchItemsInputs").value;
    this.getItems(SearchItemsInputs);
  }


  actualizaBotones = ()=>{
    let botons = globalState.getState().navBtns;
    botons.forEach((element, key) => {
      if(botons[key].id==='cambiar_mesa'){
       botons[key].visible=true;
      }

      if(botons[key].id==='iconComensales'){
       botons[key].visible=true;
      }
    });

    globalState.dispatch({
      type   : "navBtns",
      params : botons
    });
  }

  shouldComponentUpdate(nextProps){
      this.actualizaBotones();
      document.getElementById('AsideRIcon').style.display='block';
      return true;
  }

  render() {
    return (
      <div className="ContentItem">
        <div className="title">
          {globalState.getState().restaurantData.nombre}<br/>
          {globalState.getState().cashRegisterData.nombre}<br/>
          {globalState.getState().mesaData.nombre}<br/>
        </div>
        <div className="SearchItems">
            <div className="SearchItemsName"> Codigo/Nombre </div>
            <div > <input id="SearchItemsInputs" type="text" className="SearchItemsInput" onKeyUp={this.SearchItemsInput} /> </div>
        </div>
        <div className="contenedorGeneralItems">
            {this.state.items}
        </div>
      </div>
    );
  }

}

export default ContentItems;