import React, { Component } from 'react';

class PagoItemsFila extends Component {

	constructor(props){
		super(props);
    this.state={
      check:false
    };
	}

  // Este mtodo se llama despus de cada actualizacin del componente
  componentDidUpdate(prevProps) {
    
    if (prevProps.seleccionados !== this.props.seleccionados) {
      
      const isSelected = this.props.seleccionados.some(item => item.id === this.props.idI);
      if (isSelected !== this.state.check) {
        this.setState({ check: isSelected });
      }
    }
  }

  checkItem = ()=>{
    let estadoCheck = (this.state.check===true)?false:true;
    if(this.props.funClickCheck!==''){
        this.props.funClickCheck(this.props.price,this.props.idItem,estadoCheck,this.props.idI,this.props_this);

        this.setState({
          check:estadoCheck
        });
    }
    else{
      this.setState({
        check:!estadoCheck
      });
    }
    
  }

  render() {

    let styleName= {width:'80%'};
    let styleCheck= {width:'20%'};
    
    return (
      <div>
        <div className="detailistItem fontSize12" style={styleName}>{this.props.name} <br /> $ {this.props.price}</div>
        <div className="detailistItem center" style={styleCheck}> <input type="checkbox" checked = {this.state.check} onChange={this.checkItem} className="checkItem"/> </div>
      </div>  
    );
  }
}

export default PagoItemsFila;