 import React, { Component } from 'react';
 import ComensalSihoFila from './ComensalSihoFila';
import { getConsultHuesped } from '../../actions/appAction';
import {loading} from '../../global/GlobalFunctions';
import globalState from './../../global/GlobalState';
class ComensalSiho extends Component {

	constructor(props){
		super(props);
    this.state={
      filas:'',
      HuespedSiho:'',
      comensalSelect:[]
    }
	}

  componentDidMount(){
    this.searchHuesped('');
  }

  completeKeyHuesped = ()=>{
    var valueSearchHuesped= document.getElementById("searchHuesped");
    this.searchHuesped(valueSearchHuesped.value);
  }

  searchHuesped = (huespedC='')=>{

    var ajax   = getConsultHuesped(globalState.getState().userData.nitEmpresa,huespedC)
      , jsonResponse = ''
      , _this        = this

      ajax.then(function(data) {

        if(data.error===true){
            alert(data.message);
            return false;
        }else{
          console.log(data.response);
          let resposes = data.response;
          var ComensalSFila = [];
          for (var i = 0;i < resposes.length;i++) {
            let segundo_nombre = resposes[i].segundo_nombre===null?'':resposes[i].segundo_nombre;
            let segundo_apellido = resposes[i].segundo_apellido===null?'':resposes[i].segundo_apellido;
            let name= resposes[i].primer_nombre+' '+segundo_nombre+' '+resposes[i].primer_apellido+' '+segundo_apellido;
            ComensalSFila.push(<ComensalSihoFila  habitacion={resposes[i].numero_habitacion} nombre={name}  number={i} key={i} funSelectComensal={_this.addComensal.bind(_this)} />);
          }
          _this.setState({
            fila:ComensalSFila,
            HuespedSiho:resposes
          });
         return data.response;

        }

      });
  }

  addComensal(idComensal){
    let selectActual = this.state.comensalSelect;
    let compara=0;
    let elemento = this.state.HuespedSiho[idComensal];
    for (var i = 0; i < selectActual.length; i++) {
      if(selectActual[i].guest_reservations===elemento.guest_reservations)
      {
        compara=1;
      }
    }

    if(compara===0){
      if (elemento.numero_documento === "" ||
          elemento.numero_habitacion === "" ||
          elemento.numero_reserva === "" ||
          elemento.primer_nombre === ""
          ){
        alert("Se produjo un error al seleccionar el comensal, seleccionelo de nuevo, si persiste el error, recargue la pagina");
        return;
      }

      window.location='#';
      this.props.funcAumenta();
      selectActual.push(elemento);
      this.props.funcselectComensal(selectActual);

    }
    else{
      window.location='#';
    }

  }




  render() {
    return (
      <div className="ComensalSihos" >
          <div className="headerH">
            <div className="searchHeader form_content_field" style={{float:'none'}}>
              <input id="searchHuesped" type="text" className="with-label" obligatorio="false" data-icon="search" onKeyUp={this.completeKeyHuesped} placeholder="    N. habitacin"/>
            </div>
          </div>
          {this.state.fila}
      </div>
    );
  }
}

export default ComensalSiho;


