import React, { Component } from 'react';
import logo_default from '../../images/default.png'
import globalState from '../../global/GlobalState';
import {loading} from '../../global/GlobalFunctions';
import {saveItem} from '../../actions/appAction'
import ItemwinData from './ItemwinData';

const ApiConfig = window.env;


class Items extends Component {

  constructor(props){
    super(props);
    this.state={
      ingredientes:[]
    }
  }


  guardaDetallesItem = ()=> {
    let params               = {}
    ,   cantidad_item        = document.getElementById('cantidad_item').value
    ,   termino_item         = document.getElementById('termino_item').value
    ,   precio_item          = document.getElementById('precio_item').value
    ,   observaciones_item   = document.getElementById('observaciones_item').value
    ,   nuevaReceta          = false
    ,   elementsIngredientes = document.querySelectorAll('.ingredientesCampo')
    ,   ingredientes         = []
    ,   receta               = []
    ,   _this                = this;



    // console.log(this.props);return;
    // RECORRER LOS INGREDIENTES PARA VALIDAR SI SE CAMBIO LA RECETA SI INCLUYE O NO ALGUN INGREDIENTE
    if (elementsIngredientes.length>0){
      elementsIngredientes.forEach(function(element){
        // console.log(element.childNodes[0].dataset.value);
        // if(element.childNodes[0].checked===false){ nuevaReceta = true; return; }
        if(element.childNodes[0].checked===false){ return; }

        ingredientes.push({
                            id       : element.childNodes[0].dataset.id,
                            codigo   : element.childNodes[0].dataset.codigo,
                            nombre   : element.childNodes[0].dataset.nombre,
                            cantidad : element.childNodes[0].dataset.cantidad,
                            precio   : element.childNodes[0].dataset.precio,
                            incluido : element.childNodes[0].checked
                        });
      });
    }

    params = {
      id_cuenta            : globalState.getState().mesaData.id_cuenta,
      id_item              : this.props.params.id,
      codigo               : this.props.params.codigo,
      nombre               : this.props.params.nombre,
      cantidad             : cantidad_item,
      termino              : termino_item,
      precio               : precio_item,
      id_impuesto          : this.props.params.id_impuesto,
      nombre_impuesto      : this.props.params.impuesto,
      porcentaje_impuesto  : this.props.params.valor_impuesto,
      observaciones        : observaciones_item,
      id_usuario           : globalState.getState().userData.id_empleado,
      documento_usuario    : globalState.getState().userData.documento,
      nombre_usuario       : globalState.getState().userData.nombre,
      id_bodega_produccion : this.props.params.id_bodega_produccion,
      receta               : ingredientes
    }
    loading('on','Agregando Item');

    if(parseInt(cantidad_item)>0){
        saveItem(params).then(function(jsonResponse) {
          // console.log(jsonResponse);
          if (jsonResponse.status==='success'){
            params = {
              id                  : jsonResponse.id_cuenta_item,
              id_cuenta           : globalState.getState().mesaData.id_cuenta,
              id_item             : _this.props.params.id,
              codigo_item         : _this.props.params.codigo,
              nombre_item         : _this.props.params.nombre,
              cantidad            : cantidad_item,
              termino             : termino_item,
              precio              : precio_item,
              id_impuesto         : _this.props.params.id_impuesto,
              nombre_impuesto     : _this.props.params.impuesto,
              porcentaje_impuesto : _this.props.params.valor_impuesto,
              observaciones       : observaciones_item,
              receta              : ingredientes,
              id_usuario          : globalState.getState().userData.id_empleado,
              documento_usuario   : globalState.getState().userData.documento,
              usuario             : globalState.getState().userData.nombre,
            }
            // console.log(items, typeof(items) );
            // SI NO SE HAN AGREGADO AUN ITEMS A LA CUENTA DE LA MESA
            var  items = globalState.getState().headerRights.items;


            if (Array.isArray(items)===false){
              items=[];
            }

              items.push(params);

            window.location="#";
            globalState.dispatch({
              type   : "headerRights",
              params : {items:items }
            });
          }
          else{ alert(`Errorn${jsonResponse.message}`)}
          loading();
        });
    }else{
      alert('El campo cantidad es obligatorio');
       loading();
    }

    // console.log(this.props.params);

     // window.location="#";

  }

  detailItem = () => {
      var paramsItems = {
          id     : `itemModal`,
          title  : `Agregar ${this.props.params.nombre } `,
          width  : `50%`,
          height : `85%`,
          html   : <ItemwinData params={this.props.params}  />,
          tbar   : [
                  {
                    id      : '',
                    cls     : 'check_circle',
                    text    : 'Guardar',
                    handler : this.guardaDetallesItem
                  }
          ]
      };

      globalState.dispatch({
        type   : "winData",
        params : paramsItems
      });

  }



  render() {
    // console.log(this.props.params);
    // console.log(globalState.getState().userData);
    let logo = `${ApiConfig.urlServer}ARCHIVOS_PROPIOS/empresa_${globalState.getState().userData.id_host}/panel_de_control/documentos_items/${this.props.params.logo}`
    let precio = new Intl.NumberFormat().format(this.props.params.precio_venta_1)
    // , logo1 = "frontend/src/images/default.png"
    // console.log(window.location);
    return (
      <a href="#win-modal" onClick={this.detailItem.bind()}>
        <div className="Itemp" >
            <div className="card">
              <div className="card-img-cover">
                <img className="card-img"  />
              </div>
              <div className="card-content">
                <div className="card-header">
                  <div className="card-title">{this.props.params.nombre}</div>
                  <div className="card-sub-title">$ {precio}</div>
                </div>
              </div>
            </div>
        </div>
      </a>
    );
  }
}

export default Items;