import React, { Component } from 'react';
import globalState from '../../global/GlobalState';
import Tbar from './Tbar'
import {formValidate} from './Form'
// import { validateToken,getRestaurants } from './actions/appAction';
import "./Win.css"


class Win extends Component {

	constructor(props){

		super(props);

		this.state = {
			id     : '',
			title  : '',
			width  : '',
			height : '',
			html   : '',
			tbar   : ''
		}

		// id
		// cls
		// text
		// handler
		// [
		// 	{
		// 		id      : '1',
		// 		cls     : 'save',
		// 		text    : 'guardar',
		// 		handler : '',
		// 	},
		// 	{
		// 		id      : '2',
		// 		cls     : 'edit',
		// 		text    : 'editar',
		// 		handler : '',
		// 	}
		// ]

		// this.changeState(this.props.params);
		var _this = this;
		globalState.subscribe( ()=>{

			if(typeof globalState.getState().winData!=="undefined"){
				if(globalState.getState().winData.html!==this.state.html){
					_this.changeState(globalState.getState().winData);
				}
			}

		});
	}

	changeState = (params)=>{
		this.setState({
			id     : params.id,
			title  : params.title,
			width  : params.width,
			height : params.height,
			html   : params.html,
			tbar   : params.tbar
			// title   : params.title,
			// content : params.content,
		});
	}

	componentDidMount(){
		// console.log("ddddd");
		// formValidate();
	}

 	componentDidUpdate(prevProps, prevState, snapshot){
		formValidate();
	    // if (this.props.userID !== prevProps.userID) {
	    //   this.fetchData(this.props.userID);
	    // }
	    // console.log(this.props);
	    // console.log(prevProps, prevState, snapshot);
	  }

   	shouldComponentUpdate(nextProps){
      // if (this.props.params.restaurantId != nextProps.params.restaurantId){
      //   this.changeSate(nextProps);
      // }

		// formValidate();
  		return true;
  	}


	render(){
		// console.log(globalState.getState());
		const styleModal = {
			visibility : "hidden",
			opacity    : "0",
		}

		const styleWin = {
			top    : 0,
			bottom : 0,
			left   : 0,
			right  : 0,
			margin : 'auto',
			width  : this.state.width,
			height : this.state.height,
		}
		let styleSection = {
			overflowX : "hidden",
			overflowY : "auto",
			height    : "100%",
			display: "flex",
    		flexDirection: "column",
		}
		let styleHtmlContent = {
			overflowY : "auto",
			overflowX : "auto",
			width     : "100%",
			height    : "100%",
			// position  : "absolute",
		}
		return(
				<div className="win-modal" id="win-modal" /* style={styleModal}*/ >
					<div className="win-marco" style={styleWin} >
						<header >
							<div className="win-title" id="win-title-Win_Nueva_Reserva" >
								<div className="win-title-txt">{this.state.title}</div>
								<div className="win-title-btn" id="btn_close_ventana_Win_Nueva_Reserva"><a key="1" href="#">X</a></div>
							</div>
						</header>
						<section data-role="win-section" style={styleSection} >
							<Tbar params={this.state} />
							<div style={styleHtmlContent} data-cls="styleHtmlContent">
								{this.state.html}
							</div>
						</section>
					</div>
				</div>
			);
	}
}

//<div class="win-title" id="win-title-Win_Nueva_Reserva" style=" ">
//<div class="win-title-txt">Nueva Reserva</div>
//<div class="win-title-btn" id="btn_close_ventana_Win_Nueva_Reserva"></div>
//</div>
//</header>
export default Win;
