import React, { Component } from 'react';
import './teclado.css';
import globalState from '../../global/GlobalState';
import { validatePin } from '../../actions/appAction';
var verified='';
var cadenaAcum='';
class Teclado extends Component {


	constructor(props){
		super(props);
		this.state= {
			cadena:'',
			style:{
				display: 'none'
			}
		};

		globalState.subscribe( ()=>{
			// console.log(globalState.getState());
			if(typeof globalState.getState().pinState!=="undefined" & globalState.getState().type==='pinState'){
				if(globalState.getState().pinState.display!==this.state.style.display){
					verified='';
					this.setState({
						cadena:'',
						style:globalState.getState().pinState
			    	});
				}
	        }
      });

	}


 	getUrlVars = ()=> {
	    var vars      = {};
	    var parts     = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
	        vars[key] = value;
	    });
	    return vars;
	}

	agregarPin = (param, e)  => {
		// console.log(globalState.getState().userData);
		// return;
		var globalParams = globalState.getState().userData
		,	_this = this

		if(param < 12){
			verified+='*';
			if(param===11){ 
				verified   =''; 
				cadenaAcum =''; 
				param      = '';
			}
			this.setState({
				cadena : verified
		    });
		    cadenaAcum+=param;
	 	}else if(param===12 & verified!==''){
	 	 	var validateApiPin = validatePin(cadenaAcum)
	        validateApiPin.then(function(data) {
	        	// console.log(globalParams);
	        	// console.log('response = '+data.token);
				// console.log('local = '+globalParams.token_pos);
				// return;
	        	if (data.status == 'failed' ){
	        		alert(data.message);	        		
		        	_this.setState({
						cadena : ''
				    });
					cadenaAcum = '';
					verified   = '';
	        	}
	        	else if (globalParams.token_pos!==data.token) {
					let params = btoa(`token=${data.token}&nit=${globalParams.nitEmpresa}&id_sucursal=${globalParams.id_sucursal}`)
           			,	locatioNew=window.location.href.split("?");
       				// console.log(`${locatioNew[0]}?${params}`);
           			window.location=`${locatioNew[0]}?${params}`;
	        	}
	        	else{
					_this.setState({
						cadena : ''
				    });
					cadenaAcum = '';
					verified   = '';
			 		globalState.dispatch({
			            type: "pinState",
			            params: {
			                display: 'none',
			            }
		        	});
			 	}
	        });
	 	}
	 	

	}

  	render() {

			return (
				<div id="keyboardBody" style={this.state.style}>
					<div className="digit">{this.state.cadena}</div>
					<div className="BoardDiv">
						<div id="keyboard">
							<ul className="cf" id="numbers">
						    	<li onClick={this.agregarPin.bind(this,1)} ><a  className="key"><span>1</span></a></li>
						    	<li onClick={this.agregarPin.bind(this,2)} ><a  className="key"><span>2</span></a></li>
						    	<li onClick={this.agregarPin.bind(this,3)} ><a  className="key"><span>3</span></a></li>
						    	<li onClick={this.agregarPin.bind(this,4)} ><a  className="key"><span>4</span></a></li>
						    	<li onClick={this.agregarPin.bind(this,5)} ><a  className="key"><span>5</span></a></li>
						    	<li onClick={this.agregarPin.bind(this,6)} ><a  className="key"><span>6</span></a></li>
						    	<li onClick={this.agregarPin.bind(this,7)} ><a  className="key"><span>7</span></a></li>
						    	<li onClick={this.agregarPin.bind(this,8)} ><a  className="key"><span>8</span></a></li>
						    	<li onClick={this.agregarPin.bind(this,9)} ><a  className="key"><span>9</span></a></li>
						    	<li onClick={this.agregarPin.bind(this,11)} ><a className="key"><span>Del</span></a></li>
						    	<li onClick={this.agregarPin.bind(this,0)} ><a  className="key"><span>0</span></a></li>
						    	<li onClick={this.agregarPin.bind(this,12)} ><a className="key"><span>Intro</span></a></li>
					        </ul>
						</div>
					</div>
				</div>
		    );

  	}
}



export default Teclado;
