import globalState from './GlobalState.js';

export  function loading(estado='',texto=''){
	if (texto==='') {texto='Cargando...'}
	if (estado==='' || (estado!=='on' && estado!== 'off') ){
		if (globalState.getState().loadingData.estado==='on'){ estado='off' }
		else{ estado='off' }
	}
	globalState.dispatch({
      type   : "loadingData",
      params : {
                  texto  : texto,
                  estado : estado
      }
    });
}

