import React, { Component } from 'react';
import globalState from '../../global/GlobalState';
import {loading} from '../../global/GlobalFunctions';
import {solicitarPedido} from '../../actions/appAction';
import ItemCuenta from './ItemCuenta'
import './Aside.css'
import Itempago from './ItemPago';

const ApiConfig = window.env;

// import ItemBar from './items/ItemBar';
class AsideRight extends Component {

	constructor(props){
		super(props);

    this.state = {
      content : '',
      stylesA:{
          right :'-35%'
       }
    };

    globalState.subscribe( ()=>{
      if(typeof globalState.getState().headerRights!=="undefined"){
          this.changeContent();
          this.setState({
            stylesA:{
                right:globalState.getState().headerRights.estado
              }
          });
      }
    });

	}

  openWindowPay =  ()=>{
    // console.log(globalState.getState().headerRights.items);
     var ArrayItems=globalState.getState().headerRights.items;
     var valida=0;

     if(ArrayItems){
        Object.keys(ArrayItems).forEach((key) => {
          if(ArrayItems[key].comandado===true){
            valida=1;
          }
       });
     }

    if(valida>0){
      let accion = 'rotate(180deg)';
      let estado = '-35%';
      // this.setState({
      //   transform:accion
      // });

      // console.log(globalState.getState().headerRights);
      globalState.dispatch({
          type   : "headerRights",
          params : {
                     items  : globalState.getState().headerRights.items,
                     estado : estado,
                     accion : accion,
                     activo : 0
                  }
      });

      // document.getElementById('AsideRIcon').setAttribute('style',"transform: rotate(180deg);");

      // console.log(globalState.getState().headerRights);
        var send = {
            contentView : <Itempago  />
        };
        globalState.dispatch({
              type   : "contetActual",
              params : send

        });

    }else{
      alert("No hay items comandados");
    }


  }


  minimizar = ()=>{
      this.setState({
         stylesA:{
            right :'-35%',
          }
      });


      globalState.dispatch({
        type   : "headerRights",
        params : {
                   estado:'-35%',
                   items : globalState.getState().headerRights.items,
                }
      });
  }

  solicitarPedidoItems = () => {
    let itemsOld = globalState.getState().headerRights.items
    ,   items    = []
    ,   params   = {}
    ,   _this    = this

    params={
            id_cuenta         : globalState.getState().mesaData.id_cuenta,
            id_mesa           : globalState.getState().mesaData.id,
            id_usuario        : `${globalState.getState().userData.id_empleado}`,
            documento_usuario : `${globalState.getState().userData.documento}`,
            usuario           : `${globalState.getState().userData.nombre}`,
            id_caja           : `${globalState.getState().cashRegisterData.id}`,
            nombre_caja       : `${globalState.getState().cashRegisterData.nombre}`
    }

    let pedido   = solicitarPedido(params);
      // console.log(globalState.getState().mesaData);
    loading('on','Generando Pedido');
    pedido.then(function(jsonResponse) {
      if (jsonResponse.status==='success'){
        itemsOld.forEach((element, key) => {
          element.comandado = true;
          items.push(element);
        });
        globalState.dispatch({
          type   : "headerRights",
          params : {
                    items: items
                    // content: "helloo it's me"
                  }
        });
        // "urlServer"jsonResponse.id_comanda
        _this.printComanda(jsonResponse.id_comanda);
      }
      else{ alert(`Aviso\n${jsonResponse.message}`) }
      // console.log(jsonResponse);
      loading();
    });
  }

  printComanda = (id_comanda) => {
    window.open(`${ApiConfig.url}?method=printComanda&id_comanda=${id_comanda}&nit=${globalState.getState().userData.nitEmpresa}&id_sucursal=${globalState.getState().userData.id_sucursal}`, '_blank');
  }

  changeContent = () => {
    let items      = globalState.getState().headerRights.items
    , elementItem  = []
    , subtotal     = 0
    , impuestos    = 0
    , taxPercent   = 0
    , taxValue     = 0
    , content      = ''
    // console.log(globalState.getState().cashRegisterData);
    // console.log(globalState.getState().headerRights.items);
    if (typeof(items)==='object' && items !== null && JSON.stringify(items) !=='{}' ){
        items.forEach((element, key) => {
          // console.log(element, key);
          if(element.cantidad > 0){
            taxPercent = (element.porcentaje_impuesto*0.01)+1;
            subtotal   += (element.precio*element.cantidad)/taxPercent;
            impuestos  += ((((element.precio*element.cantidad))/taxPercent)*element.porcentaje_impuesto)/100

            elementItem.push(<ItemCuenta key={element.id} params={element} arrayKey={key} funcPrint={this.printComanda} /> );
          }

        });
    }

    content = (<div className="contentDetail" >
                <div className="bodyItem">
                  {elementItem}
                </div>
                <div className="resumeBar">
                    <hr align="center" />
                    <div className="totales">
                        <div>Subtotal</div>
                        <div> $ {new Intl.NumberFormat().format(subtotal.toFixed(2)) }</div>
                    </div>
                    <div className="totales">
                        <div>Impuestos</div>
                        <div> $ {new Intl.NumberFormat().format(impuestos.toFixed(2)) }</div>
                    </div>
                    <div className="totales">
                        <div>Total</div>
                        <div>$ {new Intl.NumberFormat().format((subtotal.toFixed(2))*1+(impuestos.toFixed(2))*1) }</div>
                    </div>
                    <div className="totales contentBtns">
                      <div onClick={this.openWindowPay.bind()}>
                        <i className="material-icons">receipt</i>
                        <span>Pagar</span>
                      </div>
                      <div onClick={this.solicitarPedidoItems.bind()}>
                        <i className="material-icons">room_service</i>
                        <span>Solicitar Pedido</span>
                      </div>


                    </div>
                 </div>
              </div>)
    this.setState({
      content : content
    });

  }

  globalClose = (evt)=>{
    evt.stopPropagation();
    console.log("event contentAside");
    // console.log(e);

    //
  }

  render() {

    return (
            <div className="AsideRights" style={this.state.stylesA} >
              <div className="closeDetail" onClick={this.minimizar.bind()}>
              	<i className="material-icons">keyboard_arrow_right</i>
              	Minimizar Detalle
              </div>
              {this.state.content}
            </div>
    );
  }
}

export default AsideRight;