import React, { Component } from 'react';
import globalState from '../../global/GlobalState';
import {loading} from '../../global/GlobalFunctions';
import { getCashRegisterState,getPagosCaja  } from '../../actions/appAction';

class CerrarCaja extends Component {
	constructor (props){
		super(props);
		this.state = {
			idCaja        : globalState.getState().cashRegisterData.id,
			cajero        : '',
			caja          : '',
			provision     : '',
			pagoResumido  : <tr><td colSpan="5">No hay registros</td></tr>,
			pagoDetallado : <tr><td colSpan="5">No hay registros</td></tr>,
			fecha         : ''
		}
		this.changeState();
		// console.log(globalState.getState().cashRegisterData);
	}

	changeState = ()=>{
		var ajaxEstadoCaja = getCashRegisterState(globalState.getState().cashRegisterData.id)
	    ,   _this          = this
	    ,   jsonResponse   = ''

	    loading('on','Verificando Cajas');

	    ajaxEstadoCaja.then(function(data) {
	      	jsonResponse = JSON.parse(data)
	      	console.log(jsonResponse);
	      	if (jsonResponse.status === "success"){
		        if (jsonResponse.estado==='Abierta') {
		        	_this.setState({
						cajero    : jsonResponse.nombre_usuario,
						provision : jsonResponse.provision,
						caja      : jsonResponse.nombre_caja
		        	});
		        	_this.informePagosCaja();
	        	}
	        	else if (jsonResponse.estado==='Cerrada'){
	        		alert("La caja esta cerrada!");
	        	}
      		}
      		else{
	        	alert(`Error al verificar la caja: ${jsonResponse.message}`);
      		}


	      loading()
	    })
	}

	informePagosCaja = ()=>{
		var ajaxPagosCaja = getPagosCaja(globalState.getState().cashRegisterData.id)
		,   _this         = this
		,   jsonResponse  = ''
		,	pagoDetallado = []
		,	arrayResumido = []
		,	pagoResumido  = []
		,   i             = 0
		,   j             = 0

	    ajaxPagosCaja.then(function(jsonResponse){
	    	console.log(jsonResponse);
	    	if (jsonResponse.detallado!==null) {
	    		jsonResponse.detallado.forEach(function(element) {
		    		pagoDetallado.push(<tr key={i}>
		    								<td>{element.usuario}</td>
		    								<td>{element.tipo}</td>
		    								<td>{element.forma_pago}</td>
		    								<td>{element.consecutivo}</td>
		    								<td>{new Intl.NumberFormat().format(element.valor)}</td>
	    								</tr>);
		    		i++;
				});
	    	}
	    	else{ pagoDetallado=<tr><td colSpan="5">No hay registros</td></tr> }

	    	if (jsonResponse.resumido !== null) {
	    		Object.keys(jsonResponse.resumido).forEach(function(key) {
					pagoResumido.push(<tr key={j}>
	    								<td>{jsonResponse.resumido[key].usuario}</td>
	    								<td>{jsonResponse.resumido[key].tipo}</td>
	    								<td>{jsonResponse.resumido[key].forma_pago}</td>
	    								<td>{jsonResponse.resumido[key].consecutivo}</td>
	    								<td>{new Intl.NumberFormat().format(jsonResponse.resumido[key].valor)}</td>
    								</tr>);
					j++;
					// console.log(key, jsonResponse.resumido[key])
				})
	    	}
	    	else{ pagoResumido=<tr><td colSpan="5">No hay registros</td></tr>}



        	_this.setState({
				pagoResumido  : pagoResumido,
				pagoDetallado : pagoDetallado,
				fecha         : jsonResponse.fecha
        	})
	    })

	}

	shouldComponentUpdate(nextProps, nextState){
		if (globalState.getState().cashRegisterData.id !== this.state.idCaja){
			this.setState({
				idCaja : globalState.getState().cashRegisterData.id,
        	});
			this.changeState();
			// console.log("dif");
		}
		// this.informePagosCaja();
		return true;
	}


	render(){
		let styleForm = {
			width : "380px",
			display : "block",
		}

		return(
				<div >
					<div className="form_separador" materialdesign="true"><div>{this.state.caja} (fecha: {this.state.fecha})</div></div>
					<div materialdesign="true" className="form_content_field input-filled" style={styleForm} >
						<input type="text" className="with-label" obligatorio="false" data-icon="user" id="fecha_hora_checkout" data-value="" data-required="true" data-style="none" data-label="Fecha de Salida" value={this.state.cajero} readOnly="readonly" />
						<label className="floating-label" >Cajero</label>
					</div>
					<div  materialdesign="true" className="form_content_field input-filled" style={styleForm} >
						<input type="text" className="with-label" obligatorio="false" data-icon="money" id="provision" data-value="" data-required="true" data-style="none" data-label="Fecha de Salida" value={this.state.provision} data-validate="double" readOnly="readonly"  />
						<label className="floating-label" >Provision</label>
					</div>
					<div className="form_separador" materialdesign="true"><div>Pago resumido</div></div>
					<div></div>
					<table className="table" id="InformeDataResumido">
						<thead>
							<tr>
								<td>Cajero</td>
								<td>Tipo</td>
								<td>Forma Pago</td>
								<td>Numero</td>
								<td>Valor</td>
							</tr>
						</thead>
						<tbody>
							{this.state.pagoResumido}
						</tbody>
					</table>

					<div className="form_separador" materialdesign="true"><div>Pago Detallado</div></div>
					<table className="table" id="InformeDataDetail">
						<thead>
							<tr>
								<td>Cajero</td>
								<td>Tipo</td>
								<td>Forma Pago</td>
								<td>Numero</td>
								<td>Valor</td>
							</tr>
						</thead>
						<tbody>
							{this.state.pagoDetallado}
						</tbody>
					</table>

				</div>
				);
	}
}

export default CerrarCaja;
