import React, { Component } from 'react';
import { TiCoffee } from 'react-icons/ti';
import globalState from '../global/GlobalState';
class SliderbarItems extends Component {

  constructor(props){
      super(props);
      // console.log(this.props.params);
      this.state = {
          moduleItem : this.props.moduleItem,
          params     : this.props.params
      }
  }
  clickLi = ()=>{
    // console.log(this.state.params);
    this.props.funchandleClick(this.state.params);
    globalState.dispatch({
        type   : "restaurantData",
        params : {
                    id           : this.state.params.restaurantId,
                    nombre       : this.state.params.restaurantName,
                    cambiaPrecio : this.state.params.cambiaPrecio
        }
      });
    globalState.dispatch({
      type   : "mesaData",
      params : undefined
    });


  }

  render() {
    return (
      		<li className="SliderbarLi" onClick={this.clickLi.bind()} >
      			<div className="SliderbarIcon">
      				<TiCoffee />
      			</div>
      			<div className="SliderbarText" >
      				{this.props.params.restaurantName}
      			</div>
      		</li>
    );
  }
}

export default SliderbarItems;