import React, { Component } from 'react';

class Ingredientes extends Component {

	constructor(props){
		super(props);
    // console.log(this.props);
    // cantidad: "300.00"
    // codigo: "01010504"
    // costo: null
    // id: "200"
    // nombre: "CAMARON CRUDO "
    // precio_venta: null
    // stock: null
	}


  render() {
    return (
      <div className="ingredientes" >
        <div className="ingredientesName">
          &nbsp; {this.props.params.nombre}
        </div>
        <div className="ingredientesCampo">
          <input
              type="checkbox"
              defaultChecked
              data-id       = {this.props.params.id}
              data-codigo   = {this.props.params.codigo}
              data-nombre   = {this.props.params.nombre}
              data-cantidad = {this.props.params.cantidad}
              data-precio   = {this.props.params.costo}
            />
        </div>
      </div>
    );
  }
}

export default Ingredientes;