import React, { Component } from 'react';
import globalState from '../../global/GlobalState';
import {loading} from '../../global/GlobalFunctions';
import { getComensales  } from '../../actions/appAction';

class ComensalesLista extends Component {
  constructor (props){
    super(props);
    this.state = {
      idCuenta        : globalState.getState().cashRegisterData.id,
      comensales  : <tr><td colSpan="5">No hay registros</td></tr>,
    }
    this.changeState();
    // console.log(globalState.getState().cashRegisterData);
  }

  changeState = ()=>{
    var ajaxComensales = getComensales(globalState.getState().cashRegisterData.id)
      ,   _this          = this
      ,   jsonResponse   = ''

      loading('on','Consultando Comensales');

      ajaxComensales.then(function(jsonResponse) {
          // jsonResponse = JSON.parse(data)
          console.log(jsonResponse);
          // if (jsonResponse.status === "success"){
          //   if (jsonResponse.estado==='Abierta') {
          //     _this.setState({
          //   cajero    : jsonResponse.nombre_usuario,
          //   provision : jsonResponse.provision,
          //   caja      : jsonResponse.nombre_caja
          //     });
          //     _this.informePagosCaja();
          //   }
          //   else if (jsonResponse.estado==='Cerrada'){
          //     alert("La caja esta cerrada!");
          //   }
          // }
          // else{
          //   alert(`Error al verificar la caja: ${jsonResponse.message}`);
          // }
        loading()
      })
  }


  shouldComponentUpdate(nextProps, nextState){
    if (globalState.getState().cashRegisterData.id !== this.state.idCaja){
      this.setState({
        idCaja : globalState.getState().cashRegisterData.id,
          });
      this.changeState();
      // console.log("dif");
    }
    // this.informePagosCaja();
    return true;
  }


  render(){
    let styleForm = {
      width : "380px",
      display : "block",
    }

    return(
        <div>
          <div className="form_separador" materialdesign="true"><div>Comensales de la mesa {globalState.getState().cashRegisterData.codigo}</div></div>
          <table className="table" id="InformeDataResumido">
            <thead>
              <tr>
                <td>Tipo</td>
                <td>Cantidad</td>
                <td>Documento Comensal</td>
                <td>Comensal</td>
                <td></td>
              </tr>
            </thead>
            <tbody>
              {this.state.comensales}
            </tbody>
          </table>

        </div>
        );
  }
}

export default ComensalesLista;
