 import React, { Component } from 'react';

class ComensalSihoFila extends Component {

	constructor(props){
		super(props);

	}


  selectComensal = ()=>{
      this.props.funSelectComensal(this.props.number);
  }

  render() {
    return (
        <div className="filaTable">
          <div className="columnTable">{this.props.nombre}</div>
          <div className="columnTable">{this.props.habitacion}</div>
          <div className="columnTable">
            <div className="btnNext" onClick={this.selectComensal}>
              Seleccionar
             </div>
          </div>
        </div> 
    );
  }
}

export default ComensalSihoFila;        


