import React, { Component } from 'react';
import globalState from '../../global/GlobalState';
import Btn from './Btn'
import './NavMenu.css'

class NavMenu extends Component {

  constructor(props){
    super(props);
    this.state = {
      btns : []
    }
    globalState.subscribe( ()=>{
      // console.log(globalState.getState().type);
      if(typeof globalState.getState().navBtns!=="undefined" & globalState.getState().type==="navBtns"){
        this.changeState();
      }
    });
  }

  changeState = ()=>{
    let btns = []
    ,   key = 1
    // console.log(globalState.getState().navBtns);
    // console.log(globalState.getState().navBtns);
    globalState.getState().navBtns.forEach((element, key) => {
      btns.push(<Btn key={key} params={element} />);
      key++;
    });
    // console.log(globalState.getState().navBtns);
    this.setState({
      btns : btns
    });
    // console.log(globalState.getState().navBtns);
  }

  componentDidMount(){
    if(typeof globalState.getState().navBtns!=="undefined" & globalState.getState().type==="navBtns"){
      this.changeState();
    }
  }

  render(){
  	return(
          <div className="contentMenuBtns" >
            {this.state.btns}
			   </div>
	 )
  }
}


export default NavMenu;