import React, { Component } from 'react';
import './NavMenu.css'

class SubMenu extends Component {

  constructor(props){
    super(props);
    // console.log(this.props.params);
    this.state = {
      icon : this.props.params.icon,
      text : this.props.params.text,
    }
  }

  clickLi = e =>{
    e.preventDefault();
    this.props.params.handler()
    // window.location = "#win-modal";
  }

  render(){
  	return(
        <li onClick={this.clickLi.bind()} title={this.props.params.title}>
          <a href="#" style={this.props.params.styleText}>
            <i className="material-icons">{this.state.icon}</i>
            {this.state.text}
          </a>
        </li>
    )
  }
}

export default SubMenu;