import React, { Component } from 'react';
import globalState from '../../global/GlobalState';
import SubMenu from './SubMenu'
import './NavMenu.css'

class Btn extends Component {

  constructor(props){
    super(props);
    this.submenu = React.createRef()
    // console.log(this.props);
    let submenu = []
    ,   key     = 1
    ,   style   = (this.props.params.visible===false)? { display : "none" } : { }

    // console.log(style);
    // console.log(typeof(this.props.params.menu));
    if (typeof(this.props.params.menu)==="object"){
      this.props.params.menu.forEach((element, key) => {
        submenu.push(<SubMenu key={key} params={element} />);
        key++;
      });
    }

    this.state = {
      icon    : this.props.params.icon,
      submenu : submenu,
      style   : style
    }

    // console.log(this.state.submenu.length);
  }

  clickBtn = () => {
    // e.preventDefault();
    // console.log('this is:', this.submenu.current);

    if ( this.state.submenu.length>0 ){
      // let submenus = document.querySelectorAll('.searchUl')
      // submenus.forEach((element, key) => {
      //   element.style.maxHeight = '0px';
      // });

      if (this.submenu.current.style.maxHeight === '1000%' ){
        this.submenu.current.style.maxHeight = '0px';
      }
      else{
        this.submenu.current.style.maxHeight = '1000%';
      }
    }
    else{
      this.props.params.handler();
    }

  }

  shouldComponentUpdate(nextProps){
    let style   = (nextProps.params.visible===false)? { display : "none" } : {display : "block" };

    if(this.state.style.display!==style.display){
        this.setState({
          style:style
        });
        return true;
    }else{
      return false;

    }

  }





  render(){
  	return(
        <div id={this.props.params.id} className="nav" onClick={this.clickBtn} style={this.state.style} title={this.props.params.title}>
    			<span className="menuBtn">
    				<i className="material-icons">{this.state.icon}</i>
          </span>
          <ul ref={this.submenu} className='searchUl' >{this.state.submenu}</ul>
  			</div>
      )
  }
}

export default Btn;