import React, { Component } from 'react';
import globalState from '../../global/GlobalState';

class AbrirCaja extends Component {
	constructor (props){
		super(props)
		// console.log(props.params);
	}

	render(){
		// style="width:80%; float:left"
		let styleForm = {
			width : "380px",
			display : "block",
		}

		return(
				<div >
					<div className="form_separador" materialdesign="true">
						<div >{this.props.params.nombre}</div>
					</div>
					<div /*id="form_content_field_gNuevaReserva_fecha_hora_checkout"*/ materialdesign="true" className="form_content_field input-filled" style={styleForm} /*style="width:380px; min-height:25px; display:block;"*/ >
						<input type="text" className="with-label" obligatorio="false" data-icon="user" id="fecha_hora_checkout" /*value=""*/ data-value="" data-required="true" data-style="none" data-label="Fecha de Salida" value={globalState.getState().userData.nombre} /*onchange="campoSeleccionado(this)" aria-label="Use the arrow keys to pick a date"*/ readOnly="readonly" />
						<label className="floating-label" /*for="fecha_hora_checkout"*/>Usuario</label>
					</div>
					<div /*id="form_content_field_gNuevaReserva_fecha_hora_checkout"*/ materialdesign="true" className="form_content_field input-filled" style={styleForm} /*style="width:380px; min-height:25px; display:block;"*/ >
						<input type="text" className="with-label" obligatorio="false" data-icon="money" id="provision" /*value=""*/ data-value="" data-required="true" data-style="none" data-label="Fecha de Salida" data-validate="double" /*onchange="campoSeleccionado(this)" aria-label="Use the arrow keys to pick a date" readonly="readonly"*/ />
						<label className="floating-label" /*for="fecha_hora_checkout"*/>Provision</label>
					</div>
					<div /*id="form_content_field_gNuevaReserva_fecha_hora_checkout"*/ materialdesign="true" className="form_content_field input-filled" style={styleForm} /*style="width:380px; min-height:25px; display:block;"*/ >
						<input type="text" className="with-label" obligatorio="false" data-icon="edit" id="Observacion" /*value=""*/ data-value="" data-required="true" data-style="none" data-label="Fecha de Salida" /*data-validate="date" onchange="campoSeleccionado(this)" aria-label="Use the arrow keys to pick a date" readonly="readonly"*/ />
						<label className="floating-label" /*for="fecha_hora_checkout"*/>Observacion</label>
					</div>
				</div>
				);
	}
}

export default AbrirCaja;
