import React, { Component } from 'react';
import imageCaja from '../../images/caja.png';
import globalState from '../../global/GlobalState';
import {loading,btnsState} from '../../global/GlobalFunctions';
import { getCashRegisterState,openCashRegister  } from '../../actions/appAction';
import AbrirCaja from './AbrirCaja'
import ContentMesas from '../mesas/ContentMesas'
import './Cajas.css'

// import { connect } from 'react-redux';

class Cajas extends Component {

  constructor(props){
      super(props);

      // this.state = {
      //     moduleItem         : this.props.moduleItem,
      //     restaurantId       : this.props.restaurantId,
      //     restaurantIdBodega : this.props.restaurantIdBodega,
      //     restaurantName     : this.props.restaurantName
      // }
      // console.log(props);
  }

  funClick = () =>{
    var ajaxEstadoCaja = getCashRegisterState(this.props.params.idCaja)
    ,   _this          = this
    ,   jsonResponse   = ''
    ,   params         = ''
    loading('on','Verificando Cajas');

    ajaxEstadoCaja.then(function(data) {
      jsonResponse = JSON.parse(data)
      if (jsonResponse.status === "success"){
        if (jsonResponse.estado==='Abierta') {
          globalState.dispatch({
              type   : "cashRegisterData",
              params : {
                          id     : _this.props.params.idCaja,
                          nombre : _this.props.params.nombre
              }
          });

          params = { restaurantId: _this.props.params.restaurantId }
          var send = {
              contentView : <ContentMesas params={params} />,
              params      : { }
          };
          globalState.dispatch({
                type   : "contetActual",
                params : send
          });

          let btnOptions = []
          // btnOptions[0] = {}
          // btnOptions[1] = {}
          // btnOptions[2] = {}
          btnOptions[1] = {visible:true}
          btnsState(btnOptions);
          return;

        }
        else if (jsonResponse.estado==='Cerrada'){
          params = {
              id     : ``,
              title  : `Apertura de caja`,
              width  : `50%`,
              height : `55%`,
              html   : < AbrirCaja params={_this.props.params} />,
              tbar   : [
                      {
                        id      : '',
                        cls     : 'check_circle',
                        text    : 'Abrir Caja',
                        handler : _this.funAbrirCaja,
                      },
              ],
          }
          window.location.href = "#win-modal";
        }
      }
      else{
        params = {
              id     : ``,
              title  : `Error`,
              width  : `50%`,
              height : `40%`,
              html   : `Se produjo un error al consultar la caja`,
              tbar   : ``,
        }
      }
      globalState.dispatch({
        type   : "winData",
        params : params
      });

      loading()
    })

  }

  funAbrirCaja = () => {
    let provision    = document.getElementById('provision').value
    ,   observacion  = document.getElementById('Observacion').value
    ,   params       = {}
    ,   jsonResponse = ''
    ,   _this = this

    // if (provision==='' || observacion==='' ){ alert("Aviso!\nTodos los campos son obligatorios"); return; }
    // console.log(this.props.params.restaurantId);
    // return;
    params = {
      id_caja              : this.props.params.idCaja,
      nombre_caja          : this.props.params.nombre,
      id_usuario           : globalState.getState().userData.id_empleado,
      documento_usuario    : globalState.getState().userData.documento,
      nombre_usuario       : globalState.getState().userData.nombre,
      estado               : `Abierta`,
      provision            : provision,
      observacion_apertura : observacion
    }
    loading('on','Abriendo Caja');
    openCashRegister(params).then(function(data) {
      // jsonResponse = JSON.parse(data)
      // console.log(data);
      if (data.status==='success') {
        // window.location.href = "#";
        globalState.dispatch({
            type   : "cashRegisterData",
            params : {
                        id     : _this.props.params.idCaja,
                        nombre : _this.props.params.nombre
            }
        });
        params = { restaurantId: _this.props.params.restaurantId }
        var send = {
            contentView : <ContentMesas params={params} />,
            params      : { }
        };
        globalState.dispatch({
              type   : "contetActual",
              params : send
        });
        
        window.location = "#";
      }
      else{ alert("Aviso\nSe produjo un error intentelo de nuevo") }
      loading();

    })
    // alert(provision+' '+observacion)
  }

  render() {
    return (
          [
            <a key="1" onClick={this.funClick}>
              <div className="cajas">
                <img  src={imageCaja}  width="50"/>
                <div   className="label" >
                  {this.props.params.nombre}
                </div>
              </div>
            </a>
          ]
    );
  }
}

export default Cajas;