import React, { Component } from 'react';
import {getRestaurants,getMesas} from '../../actions/appAction.js'
import {loading} from '../../global/GlobalFunctions.js'
import globalState from '../../global/GlobalState';
import './ChangeMesa.css'

class ChangeMesa extends Component {
	constructor(props){
		super(props);
		this.ambientes = React.createRef()
		this.state = {
			ambientes : '',
			mesas     : ''
		}
		this.changeEstado();
	}

	changeEstado = () => {
		loading('on','Cargando Ambientes');
		let ajaxRestaurants = getRestaurants(globalState.getState().userData.nitEmpresa,globalState.getState().userData.id_sucursal)
		,	jsonResponse    = {}
		,	_this           = this
		,	ambientes       = []
		,	i               = 1

		ajaxRestaurants.then(function(data) {
        	jsonResponse = JSON.parse(data)
        	if (jsonResponse.status==='success'){
				Object.keys(jsonResponse.restaurants).forEach((key) => {
    				// console.log(jsonResponse.restaurants[key]);
    				ambientes.push(<option key={i} value={jsonResponse.restaurants[key].id}>{jsonResponse.restaurants[key].nombre}</option>)
    				i++
				});

				_this.setState({
					ambientes : ambientes
				});
        	}
        	else{
        		alert(jsonResponse.message);
        		_this.setState({
					ambientes : ambientes
				});
        	}
			loading();
    	});
	}

	loadMesas = () => {
		let id_ambiente = this.ambientes.current.value
		if (id_ambiente===''){
			this.setState({
				mesas : ''
			});
			return;
		}
		// console.log(this.ambientes.current.value);
		loading('on','Cargando Mesas');
		let ajaxMesas    = getMesas(id_ambiente)
		,	jsonResponse = {}
		,	_this        = this
		,	mesas        = []
		,	i            = 1

		ajaxMesas.then(function(data) {
        	jsonResponse = JSON.parse(data)
        	console.log(jsonResponse);
        	if (jsonResponse.status==='success'){
				Object.keys(jsonResponse.mesas).forEach((key) => {
					if (jsonResponse.mesas[key].estado==="Abierta"){return;}
    				// console.log(jsonResponse.mesas[key]);
    				mesas.push(<option key={i} value={jsonResponse.mesas[key].id}>{jsonResponse.mesas[key].nombre}</option>)
    				i++
				});
				_this.setState({
					mesas : mesas
				});
        	}
        	else{
        		alert(jsonResponse.message);
        		_this.setState({
					mesas : ''
				});
        	}
			loading();
    	});

	}

	render(){
		return(
				<div className="contentChangeMesa" >
					<table>
						<tbody>
							<tr>
								<td>Ambiente</td>
								<td>
									<select ref = { this.ambientes } onChange={this.loadMesas} >
										<option value="" >Seleccione...</option>
										{this.state.ambientes}
									</select>
								</td>
							</tr>
							<tr>
								<td>Mesa</td>
								<td>
									<select id="mesa_destino">
										<option value="" >Seleccione</option>
										{this.state.mesas}
									</select>
								</td>
							</tr>
							<tr>
								<td colSpan="2">
									<textarea placeholder="Observacion" id="observacion_traslado"></textarea>
								</td>
							</tr>
						</tbody>
					</table>
				</div>
			)
	}
}
export default ChangeMesa;
