import React, { Component } from 'react';

class Button extends Component {
	constructor(props){
		super(props);

	}

	btnFunction = (e) =>{
		// this.attr("disabled");
		// this.disabled = true;
		// console.log('this is:', this);
		this.state.handler();
		// console.log(e);
		// console.log(this.state.handler);
		// this.disabled = false;
		e.stopPropagation();
	}

	render(){
		let styleButton = {
				color    :"#FFF",
				fontSize :"24px",
				width    :"100%",
				top      :"10px"
		}
		,	styleContentButton = {
				width : "60px",
				float : "left"
		}
		return(
				<div id={this.props.params.id} className="win-btn" style={styleContentButton} data-role="win-btn" onClick={this.props.params.handler} data-state="enable">
					<i className="material-icons" style={styleButton}>{this.props.params.cls}</i>
					<button className={this.props.cls} >{this.props.params.text}</button>
				</div>
			);
	}
}

export default Button;

