import React, { Component } from 'react';
import NumberPicker from './NumberPicker';
import {loading} from '../../global/GlobalFunctions';
import globalState from '../../global/GlobalState';
import ContentItems from '../items/ContentItems';
import { openTableAccount } from '../../actions/appAction';
class Comensales extends Component {

	constructor(props){
		super(props);

    if( Object.keys(this.props).length===0){
        this.state={
          comensaleSelects:globalState.getState().mesaData.huespedes,
          menDefault:globalState.getState().mesaData.totalC,
          funSaveC:this.actualizarComensales
      };

    }else{
      this.state={
          comensaleSelects:'',
          menDefault:0,
          funSaveC:this.guardarComensales
      };
    }
	}

  componentDidMount(){
    if( Object.keys(this.props).length===0){
      this.cargarComensales();
    }
  }

  actualizarComensales = ()=>{
    let num_hombres = document.getElementById("num_hombres").innerHTML
    , num_mujeres   = document.getElementById("num_mujeres").innerHTML
    , num_ninos     = document.getElementById("num_ninos").innerHTML
    , num_huesped   = document.getElementById("num_huesped").innerHTML
    , params        = {}
    , _this         = this
    let dataM = globalState.getState().mesaData;
    var totalComensales = 
    (parseInt(num_hombres, 10) || 0) + 
    (parseInt(num_mujeres, 10) || 0) + 
    (parseInt(num_ninos, 10) || 0);

    // console.log(_this.state.comensaleSelects);
    params = {
      id_mesa     : dataM.id,
      id_cuenta   : dataM.id_cuenta,
      nombre_mesa : dataM.nombre,
      comensales  : [
                      {tipo : "Hombres", cantidad : num_hombres },
                      {tipo : "Mujeres", cantidad : num_mujeres },
                      {tipo : "Nios", cantidad : num_ninos },
                      {tipo : "Huesped", cantidad : num_huesped,detalle:_this.state.comensaleSelects }
                  ],
      id_usuario        : globalState.getState().userData.id_empleado,
      documento_usuario : globalState.getState().userData.documento,
      nombre_usuario    : globalState.getState().userData.nombre,
    }

    loading('on','Actualizando...');
    openTableAccount(params).then(function(jsonResponse) {
      if (jsonResponse.status==='success') {
        globalState.dispatch({
            type   : "mesaData",
            params : {
                        id        : dataM.id ,
                        huespedes : jsonResponse.huespedes,
                        id_cuenta : jsonResponse.id_cuenta,
                        codigo    : dataM.codigo ,
                        nombre    : dataM.nombre,
                        totalC    : totalComensales

                    }
          });

        params = {
            id_mesa : dataM.id ,
            nombre_mesa : dataM.nombre
        }
        var send = {
            contentView : <ContentItems params={params} />
        };
        globalState.dispatch({
              type   : "contetActual",
              params : send

        });
      }
      else{ alert(`Error\n${jsonResponse.message}`) }

      loading();

    })

  }

  cargarComensales= ()=>{
     let num_hombres = document.getElementById("num_hombres");
     let num_huesped = document.getElementById("num_huesped");
     if( Object.keys(globalState.getState().mesaData).length!==0){
        num_hombres.innerHTML=globalState.getState().mesaData.totalC;
        if(typeof globalState.getState().mesaData.huespedes==='object'){
           num_huesped.innerHTML=globalState.getState().mesaData.huespedes.length;
        }
     }
  }

  guardarComensales = ()=>{
    let num_hombres = document.getElementById("num_hombres").innerHTML
    , num_mujeres   = document.getElementById("num_mujeres").innerHTML
    , num_ninos     = document.getElementById("num_ninos").innerHTML
    , num_huesped   = document.getElementById("num_huesped").innerHTML
    , params        = {}
    , _this         = this

    var totalComensales = 
    (parseInt(num_hombres, 10) || 0) + 
    (parseInt(num_mujeres, 10) || 0) + 
    (parseInt(num_ninos, 10) || 0);

    params = {
      id_mesa           : this.props.params.id,
      nombre_mesa       : this.props.params.nombre,
      comensales : [
                    {tipo : "Hombres", cantidad : num_hombres },
                    {tipo : "Mujeres", cantidad : num_mujeres },
                    {tipo : "Nios", cantidad : num_ninos },
                    {tipo : "Huesped", cantidad : num_huesped,detalle:_this.state.comensaleSelects }
                  ],
      id_usuario        : globalState.getState().userData.id_empleado,
      documento_usuario : globalState.getState().userData.documento,
      nombre_usuario    : globalState.getState().userData.nombre,
    }

    loading('on','Guardando...');
    openTableAccount(params).then(function(jsonResponse) {
      if (jsonResponse.status==='success') {
        globalState.dispatch({
            type   : "mesaData",
            params : {
                        id        : _this.props.params.id ,
                        huespedes : jsonResponse.huespedes,
                        id_cuenta : jsonResponse.id_cuenta,
                        codigo    : _this.props.params.codigo ,
                        nombre    : _this.props.params.nombre,
                        totalC    : totalComensales

                    }
          });

        params = {
            id_mesa : _this.props.params.id ,
            nombre_mesa : _this.props.params.nombre
        }
        var send = {
            contentView : <ContentItems params={params} />
        };
        globalState.dispatch({
              type   : "contetActual",
              params : send

        });
      }
      else{ alert(`Error\n${jsonResponse.message}`) }

      loading();

    })
  }

  addComensal(selectComensales){
    this.setState({
      comensaleSelects:selectComensales
    });

  }

  shouldComponentUpdate(nextProps){
      this.cargarComensales();
      return true;
  }

  render() {
      let styleButton = {
        color    :"#2a80b9",
        fontSize :"60px",
        width    :"60px"
    };

    return (
      <div className="Comensales" >
        <div className="columC">
          <div>
            <NumberPicker funcAddComensal={this.addComensal.bind(this)} huesped="false" default={this.state.menDefault}  id="num_hombres" />
            <i className="material-icons" style={styleButton}>mood</i>
            <br />
            Hombres
          </div>
        </div>
        <div className="columC">
          <div>
            <NumberPicker funcAddComensal={this.addComensal.bind(this)} huesped="false" default="0"  id="num_mujeres" />
            <i className="material-icons" style={styleButton}>face</i>
            <br />
            Mujeres
          </div>
        </div>
        <div className="columC">
          <div>
            <NumberPicker funcAddComensal={this.addComensal.bind(this)} huesped="false" default="0"  id="num_ninos" />
            <i className="material-icons" style={styleButton}>child_care</i>
            <br />
            Nios
          </div>
        </div>

        <div className="columC">
          <div>
            <NumberPicker funcAddComensal={this.addComensal.bind(this)} huesped="true" default="0" id="num_huesped"  />
            <i className="material-icons" style={styleButton}>local_hotel</i>
            <br />
            Huesped
          </div>
        </div>
        <div className="btnNext" onClick={this.state.funSaveC} >
          <i className="material-icons">receipt</i>
          <span>Siguiente</span>
        </div>
      </div>

    );
  }
}

export default Comensales;