import React, { Component } from 'react';
import globalState from '../../global/GlobalState';
import PagoItemsFila from './PagoItemsFila';
import PagoComensalFila from './PagoComensalFila';
import MetodosPagoTipo from './MetodosPagoTipo';
import {
          api,
          savePayPos,
          generateTiquet,
          rollbackDoc
        } from '../../actions/appAction';
import {loading} from '../../global/GlobalFunctions';
import ContentMesas from '../mesas/ContentMesas';

const ApiConfig = window.env;

class Itempago extends Component {

  constructor(props){
    super(props);
    this.state = {
        subtotal         : 0,
        impuesto         : 0,
        propina          : 0,
        descuentos       : 0,
        total            : 0,
        items            : '',
        display          : 'none',
        comensales       : '',
        descuentoSelect  : '',
        descuentoOptions : '',
        propinasD        : '',
        seleccionados    : [],
        isLocked         : false,
        metodoSelect     : [],
        huespedesSelect  : [],
        comensalesCont   : 0,
        clienteErp       : null,
        documento_cliente_erp : null,
        nombre_cliente_erp : null,
        descuentoData    : '',
        propinaData      : '',
        metodos          : ''
    }

    //console.log(globalState.getState().propinas);

  }

  componentDidMount(){
    this.loadItemsPrint();
    this.loadComensalesPrint();
    var metodosA   = []
    , metodos_pago = globalState.getState().metodos_pago
    , i            = 1

    for (const resP in metodos_pago) {
       metodosA.push(<MetodosPagoTipo key={i} categoria={resP}  funMetodos={this.updateComensalState.bind(this)}/>);
       i++;
    }

    this.setState({
      metodos:metodosA
    });

    var descuentos    = globalState.getState().descuentos
    ,   acum          = []
    ,   selectDisable = ""
    acum.push(<option key={i} value="none"> Seleccione descuento...</option>);
    // console.log(descuentos);
    if(typeof descuentos==='object'){
      for (var discountKey = 0;discountKey < descuentos.length;discountKey++) {
        i++;
        selectDisable = (descuentos[discountKey].permiso==='Si' && globalState.getState().userData.permiso_descuento === false)? "disabled" : "" ;
        acum.push(<option key={i} value={discountKey} disabled={selectDisable} >{descuentos[discountKey].nombre}</option>);
      }
    }


    var acumPropina = [];
    var propina = globalState.getState().propinas;
    acumPropina.push(<option key={i} value="none"> Seleccione propina...</option>);
    if(typeof propina==='object'){
      for (var propinaKey = 0;propinaKey < propina.length;propinaKey++) {
        i++;
        acumPropina.push(<option key={i} value={propinaKey}>{propina[propinaKey].nombre}</option>);
      }
    }


    this.setState({
      descuentoOptions:acum,
      propinasD:acumPropina
    });

  }
  // PRECUENTA ESTA PROHIBIDA POR LA SUPER INTENDENCIA DE INDUSTRIA Y COMERCIO
  printPrecuenta = () => {
    let ItemSeleccionado     = this.state.seleccionados;
    ItemSeleccionado.forEach(element => {
      if(element.receta){delete element.receta}
      delete element.cantidad

    });
    let totales = this.calcularTotales(false);

    console.log({items:ItemSeleccionado,totales:totales})
    const form = document.createElement('form');
    form.method = 'POST';
    form.action = `${ApiConfig.url}?method=printPrecuenta&nit=${globalState.getState().userData.nitEmpresa}&id_sucursal=${globalState.getState().userData.id_sucursal}`;
    form.target = '_blank'; // Abre en una nueva ventana
    form.style.display = 'none'; // Asegura que el formulario no sea visible

    // Crear un campo de entrada oculto para los datos
    const input = document.createElement('input');
    input.type = 'hidden';
    input.name = 'data';
    input.value = JSON.stringify({items:ItemSeleccionado,totales:totales});
    form.appendChild(input);

    // Agregar el formulario al cuerpo del documento
    document.body.appendChild(form);
    // Enviar el formulario
    form.submit();
    // Eliminar el formulario del DOM despus de enviarlo
    document.body.removeChild(form);

  }

  updateComensalState(metodoSelect){
    this.setState({
      metodoSelect:metodoSelect
    });
  }

  ChangeStateComensales(estado,tipo,keys,comensal,documento_comensal, nombres='', apellidos='', correo_comensal='', tipo_documento_comensal='', clientePos = false, tipoPersona=''){
    // console.log(estado,tipo,keys,comensal,documento_comensal);
    var count = this.state.comensalesCont;
    var huespedSeleccion = [];
    if(tipo==='huesped'){
      if(estado===true){
        huespedSeleccion.push(globalState.getState().mesaData.huespedes[keys]);
        count=0;
      }
    }
    else if(estado===true){
      // console.log(comensal,documento_comensal);
      if (comensal !=='' && documento_comensal !=='' && comensal!==undefined && documento_comensal!==undefined) {
        // console.log('in');
        huespedSeleccion.push(
                              {
                                "id_reserva"          : "",
                                "numero_habitacion"   : "",
                                "id_comensal"         : "",
                                "id"                  : "",
                                "comensal"            :comensal,
                                "nombres"             :nombres,
                                "apellidos"           :apellidos,
                                "documento_comensal"  :documento_comensal,
                                "correo"              :correo_comensal,
                                "tipoDocumento"       :tipo_documento_comensal,
                                "clientePos"          :clientePos,
                                "tipoPersona"         :tipoPersona
                              }
                              );
        count=0;
      }
      else{
        count=1;
      }
    }else{
      count=0;

    }
    // console
    //console.log({debug:"ItemsPago",array:huespedSeleccion})
    this.setState({
      comensalesCont:count,
      huespedesSelect:huespedSeleccion,
      clienteErp : null
    });

  }


  loadComensalesPrint(){
    var comesalesA = [];
    let cont       = 0;
    let _this = this;
    for (var i = 0;i < parseInt(globalState.getState().mesaData.totalC);i++) {
      cont++;
      let nameV= `comensal ${cont}`;
      comesalesA.push(<PagoComensalFila idK={i} key={i}  type="normal" funChangeStateC={this.ChangeStateComensales.bind(this)} name={nameV} />);
    }

    let arrayHuespedes =  globalState.getState().mesaData.huespedes;

    if(arrayHuespedes.length>0){
      Object.keys(arrayHuespedes).forEach((key) => {
        let nameC = arrayHuespedes[key].comensal+' ('+arrayHuespedes[key].numero_habitacion+')';
        comesalesA.push(<PagoComensalFila idK={key} key={i} type="huesped" funChangeStateC={this.ChangeStateComensales.bind(this)} name={nameC} guestId={arrayHuespedes[key].id_comensal}  onHuespedSeleccionado={this.manejarHuespedContler.bind(_this)} />);
        i++;
      });

    }

    // console.log(comesalesA);

     this.setState({
      comensales:comesalesA
     });
  }

  // Si un huesped vene de un pedido contler, se selecciona los items del pedido y se bloquean los otros
  manejarHuespedContler(guestId){
    let allItems = globalState.getState().headerRights.items
    let filteredItems = allItems.filter(item => item.id_comensal === guestId)
    if(filteredItems.length === 0){
      this.setState({
        isLocked:false,
        seleccionados: [],
      });
    }else{
      this.setState({
        isLocked:true,
        seleccionados: filteredItems,
      });
    }
  }
  
  // crea un useEffect para manejar el cambio de seleccionados. useEffect de react por clases
  componentDidUpdate(_, prevState){
    if (this.state.seleccionados !== prevState.seleccionados){
      this.loadItemsPrint(this.state.isLocked);
      this.calcularTotales();
    }
  }


  seleccionarItems(_,idItem,estadoCheck,keys){

    let ItemSeleccionado = this.state.seleccionados;
    let elemento      = globalState.getState().headerRights.items[idItem];
    // console.log(elemento);
    if(estadoCheck===true){
        ItemSeleccionado.push(elemento);
    }else{

        for (var i = 0; i < ItemSeleccionado.length; i++) {
          if(parseInt(ItemSeleccionado[i].id)===parseInt(keys)){
            ItemSeleccionado.splice( i, 1 );
            break;
          }
        }
    }

    this.setState({
        seleccionados:ItemSeleccionado
    });
    // console.log(pricesItem,idItem,estadoCheck,keys);
    // console.log("selected");
    // console.log(globalState.getState().headerRights.items[idItem]);
    this.calcularTotales();
  }

  calcularTotales = (isVoid=true)=>{

    let ItemSeleccionado     = this.state.seleccionados;
    var descuentos_select    = document.getElementById('descuentos_select');
    var elementos_descuentos = globalState.getState().descuentos[descuentos_select.value];
    var propina_input        = document.getElementById('propina_input').value;
    var propina_select       = document.getElementById('propina_select');
    var elementos_propina    = globalState.getState().propinas[propina_select.value];
    let subtotal  = 0;
    let taxPercent= 0;
    let descuentoTotal = 0;
    let propina   = 0;
    let impuestos = 0;
    for (var i = 0; i < ItemSeleccionado.length; i++) {
        let precio = ItemSeleccionado[i].precio;
        if(elementos_descuentos){
           let descuentoProv =(precio*elementos_descuentos.porcentaje)/100;
           precio =precio-descuentoProv;
           descuentoTotal +=descuentoProv;
        }
        taxPercent = (ItemSeleccionado[i].porcentaje_impuesto*0.01)+1;
        subtotal   += precio/taxPercent;
        impuestos  += ((precio/taxPercent)*ItemSeleccionado[i].porcentaje_impuesto)/100;
    }

    let totales = subtotal;
    // SI DIGITARON PROPINA ENTONCES NO HACER EL CALCULO POR %
    if (propina_input>0){
        propina =propina_input*1;
        totales = totales + propina;

    }
    else{
      if(elementos_propina){
        let propinaProv=(totales*elementos_propina.porcentaje)/100;
        propina+=propinaProv;
        totales = totales + propinaProv;
      }
      else{
        propina = 0;
      }
    }

    //totales = totales + impuestos;
    totales = Math.round(totales + impuestos);
    var newstatetotales = {
      subtotal       : subtotal,
      descuentos     : descuentoTotal,
      impuesto       : impuestos,
      propina        : propina,
      descuentoData : elementos_descuentos,
      total          : totales
    };
    this.setState(newstatetotales);
    if(!isVoid){return newstatetotales}
  }

  cambiarVentana = (elemento1,elemento2)=>{

      document.getElementById(elemento1).style.display='block';
      document.getElementById(elemento1+'P').classList.add("selectOptionItem");
      document.getElementById(elemento2).style.display='none';
      document.getElementById(elemento2+'P').classList.remove("selectOptionItem");
  }



  loadItemsPrint = (islocked=false) =>{

    var ArrayItems=globalState.getState().headerRights.items;
    var item = [];
    var i=0;
    var contGlobal=0;
    Object.keys(ArrayItems).forEach((key) => {
      if(ArrayItems[key].comandado===true){
        for (var contI = 0; contI < parseInt(ArrayItems[key].cantidad); contI++) {
          contGlobal++;
          item.push(<PagoItemsFila  idI={ArrayItems[key].id} name={ArrayItems[key].nombre_item}  key={contGlobal} idItem={key} price={ArrayItems[key].precio} tax={ArrayItems[key].porcentaje_impuesto} seleccionados={this.state.seleccionados} funClickCheck={(islocked)?'':this.seleccionarItems.bind(this)} />);
        }
      }

      i++;
      if(i===ArrayItems.length){
       this.setState({
          items : item
        });
      }

    });
  }

  propinaSelect = ()=>{
    let propina_select = document.getElementById('propina_select').value;
    this.setState({
      propinaData:globalState.getState().propinas[propina_select]
    });
    this.calcularTotales();
  }

  propinaInput = ()=>{
    let propina = document.getElementById('propina_input').value;
    if (!/^([0-9])*$/.test(propina)){
      alert("Debe digitar solo valores numericos en el campo propina");
      document.getElementById('propina_input').value = 0;
      return;
    }
    // console.log(propina);
    // return;
    this.setState({
      propina    : propina*1,
    });

    this.calcularTotales();
  }



  totalCheck = e =>{
    let checkItem = document.getElementsByClassName('checkItem');
    for (var objectEl in checkItem) {
      console.log(checkItem[objectEl]);
      if(typeof checkItem[objectEl]==='object'){
        checkItem[objectEl].click();
      }
    }
  }

  generaPago = async ()=>{
    

    let metodosV=this.state.metodoSelect;
    let acumMetodosP= 0;
    for (var i = 0; i < metodosV.length; i++) {
      if(parseInt(metodosV[i].valor)>0){
       acumMetodosP+=parseInt(metodosV[i].valor);
       // SI ES PAGO CON TARJETA VALIDAR QUE SE INGRESO EL # DE APROBACION Y TARJETA
       if (metodosV[i].tipo==='Tarjeta Credito' || metodosV[i].tipo==='Tarjeta Debito'){
        if (metodosV[i].n_aprobacion===null || metodosV[i].n_aprobacion==='' ) {
          alert(`No se ha ingresado el numero de aprobacion para el metodo de pago ${metodosV[i].nombre}`);
          return;
        }
        if (metodosV[i].n_tarjeta===null || metodosV[i].n_tarjeta==='' ) {
          alert(`No se ha ingresado los digitos de la tarjeta para el metodo de pago ${metodosV[i].nombre}`);
          return;
        }
       }
      }
    }

    if(parseInt(acumMetodosP) < parseInt(this.state.total) ){
      let diferencia = parseInt(this.state.total) - parseInt(acumMetodosP)  ;
      diferencia = new Intl.NumberFormat().format(diferencia.toFixed(2));
      alert('No esta completa el total de la factura, hace falta $'+diferencia);
    }
    else if(parseInt(acumMetodosP) > parseInt(this.state.total) ){
      let diferencia = parseInt(acumMetodosP) - parseInt(this.state.total);
      diferencia = new Intl.NumberFormat().format(diferencia.toFixed(2));
      alert('Los metodos de pago superan el valor total a pagar por $'+diferencia);
    }
    else if(this.state.seleccionados.length>0 ){
      if(this.state.comensalesCont>0 || this.state.huespedesSelect.length > 0 || this.state.clienteErp>0){
        let response={
          itemsSelect     : this.state.seleccionados,
          comensales      : this.state.comensalesCont,
          subtotalt       : this.state.subtotal,
          impuestot       : this.state.impuesto,
          descuentost     : this.state.descuentos,
          propinat        : this.state.propina,
          mesa            : globalState.getState().mesaData,
          user            : globalState.getState().userData,
          restaurant      : globalState.getState().restaurantData,
          caja            : globalState.getState().cashRegisterData,
          totalt          : this.state.total,
          totalMetodos    : acumMetodosP,
          huespedesSelect : this.state.huespedesSelect,
          descuentoData   : this.state.descuentoData,
          propinaData     : this.state.propinaData,
          metodoPago      : this.state.metodoSelect,
          clienteErp      : this.state.clienteErp,
          documento_cliente_erp      : this.state.documento_cliente_erp,
          nombre_cliente_erp      : this.state.nombre_cliente_erp
        };

        let acumulado_cheque_cuenta = 0;
        let acumulado_cortesia = 0;
        
        var error_huesped = false;
        response.metodoPago.forEach(  (element) => {
                                      // console.log("tipo: "+element.tipo);
                                      // console.log("valor: "+element.valor);
                                      // console.log("comensales: "+this.state.comensalesCont);
                                      
                                      if (element.tipo==="Cheque Cuenta"){ acumulado_cheque_cuenta += element.valor  }
                                      if (element.tipo==="Cortesia"){ acumulado_cortesia += element.valor  }

                                      if (element.tipo==="Cheque Cuenta" && element.valor>0 && this.state.comensalesCont>0){
                                        error_huesped = true;
                                        console.log(element)

                                      }
                                    }
                                  );
       
        if (error_huesped===true){
          alert("No se puede generar un cheque cuenta con un comensal general! \nDebe seleccionar un huesped para poder generar un cheque cuenta");
          return;
        }
       
        loading('on','Generando un momento por favor');
        let res  = await api.get({
                                  "method" : "get_tope_facturacion"
                                });

        if ( ((response.subtotalt-acumulado_cheque_cuenta)-acumulado_cortesia) > res.tope && res.tope>0 && (response.huespedesSelect.length===0 && this.state.clienteErp==null) ) {
          alert("Debe diligenciar los datos del comensal o seleccionar un cliuente ERP para facturar");
          loading();
          return;
        }

        this.procesarPago(response);

      }else{
        alert("Debe seleccionar por lo menos un comensal");
      }
    }else{
      alert("Debe seleccionar por lo menos un item!");
    }
  }

  get_clients_erp = async (event) =>{
    if(event.key !== 'Enter'){return;}

    loading('on','Buscando...');
    let terceros_erp  = await api.get({
                                    "method" : "get_clients",
                                    "params" : [ `value=${event.target.value}` ]
                                  });
    this.setState({terceros_erp})
    loading();

  }

  unCheck = (currentCheckbox) =>{
    let checked = currentCheckbox.target.checked
    ,   value = currentCheckbox.target.value
    // console.log(currentCheckbox.target.dataset.document)
    const checkboxes = document.getElementsByClassName("pagoComensal");
    for (let i = 0; i < checkboxes.length; i++) {
        checkboxes[i].checked = false;
    }

    if(checked){
      this.setState({ 
          clienteErp : value , 
          documento_cliente_erp: currentCheckbox.target.dataset.document, 
          nombre_cliente_erp:currentCheckbox.target.dataset.nombre  
      })
    }
    else{
      this.setState({clienteErp:null,documento_cliente_erp:null,nombre_cliente_erp:null})
    }
    
  }

  render() {
    let styleName= {width:'80%'};
    let styleCheck= {width:'20%'};
    let styleClientLi = { 
                          "display" : "flex",
                          "justifyContent" : "center",
                          "alignItems" : "center"
                        }
    return (
        <div className="BodyPay">
          <div className="columItem">
            <div className="contenidoItem">
              <div className="titleItem">
                <div className="detailtitleItem"  style={styleName}>Item</div>
                <div className="detailtitleItem center"  style={styleCheck}> <input type="checkbox" onClick={this.totalCheck} /></div>
              </div>
              <div className="listItem">
                  {this.state.items}
              </div>
            </div>
            <div className="resumeItem">
                <div className="titleItem">
                  <div className="detailtitleItem titleResumen">Subtotal</div>
                  <div className="detailtitleItem center priceResumen"> ${new Intl.NumberFormat().format(this.state.subtotal.toFixed(2)) }</div>
                </div>
                <div className="titleItem">
                  <div className="detailtitleItem titleResumen">Descuentos</div>
                  <div className="detailtitleItem center priceResumen"> ${new Intl.NumberFormat().format(this.state.descuentos.toFixed(2)) }</div>
                </div>
                <div className="titleItem">
                  <div className="detailtitleItem titleResumen">Propina</div>
                  <div className="detailtitleItem center priceResumen"> ${new Intl.NumberFormat().format(this.state.propina.toFixed(2)) }</div>
                </div>
                <div className="titleItem">
                  <div className="detailtitleItem titleResumen">Impuestos</div>
                  <div className="detailtitleItem center priceResumen"> ${new Intl.NumberFormat().format(this.state.impuesto.toFixed(2)) }</div>
                </div>
                <div className="titleItem">
                  <div className="detailtitleItem titleResumen">Total</div>
                  <div className="detailtitleItem center priceResumen"> ${new Intl.NumberFormat().format(this.state.total.toFixed(2))}</div>
                </div>
            </div>
          </div>
          <div className="columItem">
              <div className="contenidoItem">
                <div className="titleItem">
                  <div id="metodosPagoP" className="detailtitleItem optionItem selectOptionItem" onClick={()=>this.cambiarVentana("metodosPago","comensales")} >Metodos de Pago</div>
                  <div id="comensalesP" className="detailtitleItem center optionItem" onClick={()=>this.cambiarVentana("comensales","metodosPago")}  > Comensales</div>
                </div>
                <div className="listItem flowRoot" id="metodosPago" >
                    <div className="acordeon">
                      {this.state.metodos}
                    </div>
                </div>
                <div className="listItem" id="comensales" >
                  {this.state.comensales}

                  <div 
                    className=""
                    style={
                      {
                        "width":"100%",
                        "display":"flex",
                        "justifyContent":"space-evenly",
                        "alignItems": "center",                      
                        "cursor":"pointer",
                        "height": "40px ",
                        "fontWeight": "600",
                        "backgroundColor": "#EEE",
                      }}
                  >
                    <span>Buscar clientes</span>
                    <input type="text" placeholder="documento o nombre" onKeyUp={async (event)=>this.get_clients_erp(event)} />
                    <i className="material-icons" title="Buscar cliente" onClick={this.modal_search_erp_client}>search</i>
                  </div>
                  <ul
                    style={{
                      "display" : "grid",
                      "gridTemplateColumns" : "repeat(3, auto)",
                      "overflowX" : "hidden"
                    }}
                  >
                      { this.state.terceros_erp && 
                        this.state.terceros_erp.map((element,index) => {
                          return (
                                  <React.Fragment key={element.id}>
                                    <li style={styleClientLi} key={`input-${element.id}`}>
                                      <input 
                                        type="checkbox" 
                                        class="pagoComensal" 
                                        checked = { this.state.clienteErp===element.id && true } 
                                        value={element.id} 
                                        data-document = {element.documento}
                                        data-nombre= {element.nombre}
                                        onChange={this.unCheck}/>
                                    </li>
                                    <li style={styleClientLi} key={`documento-${element.id}`}>{element.documento}</li>
                                    <li key={`nombre-${element.id}`}>{element.nombre}</li>
                                  </React.Fragment>   
                          )                     
                        })
                      }
                  </ul>
                </div>

              </div>
              <div className="resumeItem">
                <div className="descuentosD">
                  <select id="descuentos_select" onChange={this.calcularTotales}>
                    {this.state.descuentoOptions}
                  </select>
                </div>
                <div className="descuentosD propinaInput">
                  <select id="propina_select" onChange={this.propinaSelect}>
                    {this.state.propinasD}
                  </select>
                  <input type="text" id="propina_input" placeholder="Digite la Propina" onKeyUp={this.propinaInput} />
                </div>
                <div className="totales contentBtns">
                 
                  <div style={{textAlign: 'center'}} onClick={async () => {await this.generaPago();}}>
                    <i className="material-icons">attach_money</i>
                    <span>Facturar</span>
                  </div>
                  
                  <div style={{textAlign: 'center'}} onClick={this.printPrecuenta.bind()}>
                    <i className="material-icons">receipt</i>
                    <span>Precuenta</span>
                  </div>
                </div>
              </div>
          </div>
        </div>
    );
  }
  
  ///Validar el consecutivo
  consecutivoValido = (valor) =>{
    return (valor !== '' && valor !== null && valor !== '0' && valor !== 0);
  }
  
  //Mostrar el tiquet pos en PDF
  abrirTicket = (idPos, userData) =>{
    const { nitEmpresa, id_sucursal } = userData;
    const url = `${ApiConfig.url}?method=printTiquet&id_documento=${idPos}&nit=${nitEmpresa}&id_sucursal=${id_sucursal}`;
    window.open(url, '_blank');
  }
  
  //Mostrar la pantalla de mesas del POS
  volverAMesas = (restaurantId) =>{
    const params = { restaurantId };
    const send = {
      contentView: <ContentMesas params={params} />,
      params: {}
    };

    globalState.dispatch({
      type: "contetActual",
      params: send
    });
  }

  mostrarError = (mensaje) =>{
    alert(mensaje || "Ha ocurrido un error");
  }

  procesarPago = async (response) =>{
    try {
      //Guarda el ticket y generar el id_pos
      const jsonResponse = await savePayPos(response);

      if (jsonResponse.status === 'success') {
        //Mover cuentas, facturas e inventario, asignar el consecutivo
        const responseJson = await generateTiquet({ ...response, id: jsonResponse.idPos });

        //Si el consecutivo es vacio no se imprime el ticket
        if (responseJson.status === true && 
            (
              this.consecutivoValido(responseJson.consecutivo ?? "") ||
              this.consecutivoValido(responseJson.id_remision ?? "") ||
              this.consecutivoValido(responseJson.id_FV ?? "")
            )){

          this.abrirTicket(jsonResponse.idPos, globalState.getState().userData);
          this.volverAMesas(globalState.getState().restaurantData.id);
        } 
        else {
          //Si no se genero el ticket o no es valido
          const responseRollback = await rollbackDoc({ ...response, id: jsonResponse.idPos });
          if(responseRollback.status === true){
            const mensaje = responseJson.message || "Ha ocurrido un error, por favor intntelo de nuevo";
            this.mostrarError(mensaje);
            this.volverAMesas(globalState.getState().restaurantData.id);
          }else{
            this.mostrarError("Ha ocurrido un error verifique que los items sigan disponibles para facturar");
            this.volverAMesas(globalState.getState().restaurantData.id);
          }
        }

      } else if (jsonResponse.message !== '') {
        this.mostrarError(jsonResponse.message);
      }

    } catch (error) {
      this.mostrarError("Error inesperado. Por favor intente nuevamente.");
      console.error(error);
    } finally {
      loading();
    }
  }
}

export default Itempago;