import React, { Component } from 'react';
import ItemMesa from './ItemMesa';
import globalState from '../../global/GlobalState';
import {loading} from '../../global/GlobalFunctions';
import './ItemMesa.css'
import { getMesas,getEstadoMesas } from '../../actions/appAction';

class ContentMesas extends Component {

	constructor(props){
		super(props);
    this.state ={
      estados : <td></td>,
      mesas   :'',
    };
    var  restaurantId = this.props.params.restaurantId
    , ajaxMesas       = getMesas(restaurantId)
    , ajaxEstadoMesas = getEstadoMesas()
    , _this           = this
    , jsonResponse    = ''
    , params          = {}
    , estados         = []
    , mesas           = []
    , i               = 1

    // console.log(globalState.getState().cashRegisterData);

    loading('on','Cargando Mesas');
    ajaxMesas.then(function(data) {
      jsonResponse = JSON.parse(data)
      if (jsonResponse.status==='success'){
        Object.keys(jsonResponse.mesas).forEach((key) => {
          // console.log(jsonResponse.mesas[key]);
          // params = { idMesa : jsonResponse.mesas[key].id ,nombre : jsonResponse.mesas[key].nombre, restaurantId : restaurantId }
          mesas.push( <ItemMesa params={jsonResponse.mesas[key]} key={i} />);
          i++;
        });
      }
      else{
          let styleError = {
            fontSize   : '14px',
            textAlign  : 'center',
            width      : '100%',
            paddingTop : '5px',
            fontWeight : 'bold'
          }
          mesas.push( <div key={i} style={styleError} >{jsonResponse.message}</div>);
      }

      _this.setState({
        mesas:mesas
      });

      loading();

    });

    i = 1
    ajaxEstadoMesas.then(function(data) {
      jsonResponse = JSON.parse(data);
      // console.log(jsonResponse);
      if (jsonResponse.status==='success'){
        Object.keys(jsonResponse.estados).forEach((key) => {
          // console.log(jsonResponse.estados[key].nombre);
          let styleState = {backgroundColor:jsonResponse.estados[key].color}
          estados.push( <td key={i} style={styleState} ></td>);
          estados.push( <td key={i*100} >{jsonResponse.estados[key].nombre}</td>);
          estados.push( <td key={i*1000} ></td>);
          i++;
        });

      }
      else{
        estados.push( <td key={i} >{jsonResponse.message}</td>);
        // jsonResponse.message
      }
      _this.setState({
        estados:estados
      });
      // console.log(estados);
    });



     this.actualizaBotones();
	}

  cerrarCaja = ()=>{

  }



  actualizaBotones = ()=>{
    let botons = globalState.getState().navBtns;
    botons.forEach((element, key) => {
      if(botons[key].id==='monitor'){
       botons[key].visible=true;
      }
    });

    globalState.dispatch({
      type   : "navBtns",
      params : botons
    });


  }

    shouldComponentUpdate(nextProps){
      this.actualizaBotones();
    
      return true;
    }


  render() {
    return (
      <div className="contentMesa" key='99999999' >
        <div className="contentEstadosMesa">
          <table>
            <thead>
              <tr>
                <td>{globalState.getState().restaurantData.nombre}</td>
              </tr>
              <tr>
                <td>{globalState.getState().cashRegisterData.nombre}</td>
              </tr>
            </thead>
          </table>
          <table>
            <tbody>
              <tr>
                {this.state.estados}
              </tr>
            </tbody>
          </table>
        </div>
        {this.state.mesas}
      </div>
    );
  }
}

export default ContentMesas;