 import React, { Component } from 'react';
import ComensalSiho from './ComensalSiho';
import globalState from '../../global/GlobalState';

class NumberPicker extends Component {

	constructor(props){
		super(props);
		this.state={
      contentNumber:parseInt(this.props.default),
      id : this.props.id
		};


	}


  selectComensal(selectComensal){
    this.props.funcAddComensal(selectComensal);
  }



	mostrarComensales= e =>{

    if(this.props.huesped==='true'){
        var paramsComensal = {
          id     : `sihoModal`,
          title  : `Agregar Huesped `,
          width  : `50%`,
          height : `85%`,
          html   : <ComensalSiho params={this.props}  funcAumenta={this.aumentar.bind()} funcselectComensal={this.selectComensal.bind(this)} />
       };
      globalState.dispatch({
        type   : "winData",
        params : paramsComensal
      });
      e.preventDefault();
      window.location = "#win-modal";
    }else{

      this.aumentar();
    }
	}

  aumentar= e =>{
     var number = this.state.contentNumber;
      number++;
      this.setState({
        contentNumber:number
      });

  }




	disminuir= ()=>{
		var number = this.state.contentNumber;
		number--;
    if(number >= 0){
      this.setState({
        contentNumber:number
      });
    }

	}

  render() {
    return (
      <div className="numberPickers" >
        <div className="numberContentFlecha">
          <div className="arrowAumentar" onClick={this.mostrarComensales.bind()}></div>
        </div>
        <div className="numberContent" id={this.state.id} >{this.state.contentNumber}</div>
        <div className="numberContentFlecha">
           <div className="arrowDisminuir" onClick={this.disminuir} ></div>
        </div>
      </div>
    );
  }
}

export default NumberPicker;        


