import React, { Component } from 'react';
import Logo from './Logo';
import UserInfo from './UserInfo'
import globalState from '../global/GlobalState';
import NavMenu from './NavMenu/NavMenu'
import { MdBlock } from 'react-icons/md';
import { TiThMenuOutline,TiChevronLeftOutline,TiChevronRightOutline } from 'react-icons/ti';

class Header extends Component {

  constructor(props){
  		super(props);
      this.state = {
        transform:'rotate(180deg)',
        display : 'none'
        // right:'-35%'
      };
      globalState.subscribe( ()=>{
        if(typeof globalState.getState().headerRights!=="undefined"){
            if(typeof globalState.getState().headerRights.activo==="undefined" & typeof globalState.getState().headerRights.estado!=="undefined"){
                let accion = (globalState.getState().headerRights.estado==='none')?'rotate(180deg)':'rotate(0deg)';
                 console.log("header "+accion);
                 this.setState({
                    transform : accion
                 });
            }
        }
      });
  }

  silderShow = ()=>{
  	this.props.actionSliderShow();

  }


  openDetailItem=()=>{
    let accion = (this.state.transform==='rotate(180deg)')?'rotate(0deg)':'rotate(180deg)';
    let estado = (this.state.transform==='rotate(180deg)')?'0%':'-35%';
    this.setState({
      transform:accion
    });

    globalState.dispatch({
        type   : "headerRights",
        params : {
                   items : globalState.getState().headerRights.items,
                   estado:estado,
                   activo:0
                }
    });

  }

  openPin=()=>{
    globalState.dispatch({
    type   : "pinState",
        params : {
                  display:'block'
                }
    });
  }

  render() {
    return (
      <div className="header" >
      	<TiThMenuOutline className='slideIcon' onClick={this.silderShow} style={{display:"none"}}/>
        <Logo /> 
        <TiChevronRightOutline id="AsideRIcon" className='navigate_next' style={this.state} onClick={this.openDetailItem} />
        <NavMenu />
        <UserInfo /> 
      </div>
    );
  }
}

export default Header;